import { getField, updateField } from 'vuex-map-fields';

import {
  mixinDialogMutations,
  mixinDialogActions,
  mixinDialogGetters
} from '../../mixins/vuex_dialogs';

export default {
  namespaced: true,
  state: {
    activeTab: localStorage.getItem('settings-active-tab') ?
      JSON.parse(localStorage.getItem('settings-active-tab')) :
      {
        tab: 0,
        component: 'general'
      },
    dialogs: {
    },
    fbSettings: {
      sync: 0,
      interval: 0,
      auto_publish: 0,
    },
    settings: {
      general: {
        use_global: false,
        change_ip_from_ui: false,
        general_wait_proxy_change_ip: 0,
        general_wait_before_change_ip: 0,
        experimental_feature: false,
      },
      intervals: {
        sync: 0,
        sync_time: 0,
        fp_auto_publish: 0,
        // fp_auto_publish_time: 0,
        comments: 0,
        comments_time: 0,
        comments_mode: null
      },
      upload: {
        max_upload_jobs: 0,
        url_params_target: 'link'
      },
      notifications: {
        notifications_telegram: null,
        notifications_telegram_tokens: null,
        notifications_telegram_cabs: null,
        notifications_telegram_moderation: null,
        notifications_telegram_billing: null,
        notifications_telegram_billing_threshold: null,
        notifications_telegram_proxy_expirity: null,
        notifications_alarmerbot_key: null
      },
      costs: {
        costs_binom: null,
        costs_binom_api_key: null,
        costs_binom_interval: 60,
        costs_binom_object: 'campaign',
        costs_binom_period: 'today_and_yesterday',
        costs_binom_token: 1,
        costs_binom_target: 'all',
        costs_binom_timeout: '250',
        costs_binom_url: null,
        costs_binom_sources: null,
        costs_binom_groups: null,
        costs_binom_campaigns: null,
        costs_keitaro: null,
        costs_keitaro_api_key: null,
        costs_keitaro_interval: 60,
        costs_keitaro_object: 'campaign',
        costs_keitaro_period: 'today_and_yesterday',
        costs_keitaro_subid: 1,
        costs_keitaro_target: 'all',
        costs_keitaro_timeout: '250',
        costs_keitaro_timezones_reset: null,
        costs_keitaro_url: null,
        costs_keitaro_sources: null,
        costs_keitaro_groups: null,
        costs_keitaro_campaigns: null
      },
      comments: {
        trigger_words_mode: null,
        trigger_words: null
      },
      domains: {
        cf_api_key: null,
        // cf_email: null
      }
    },
    globals: {
      state: false,
      admin_id: null
    },
    binomData: {},
    keitaroData: {}
  },
  getters: {
    ...mixinDialogGetters,
    getField,
    
    activeTab: (state) => state.activeTab,
    settings: state => state.settings,
    globals: state => state.globals,
    binomData: state => state.binomData,
    keitaroData: state => state.keitaroData,
    fbSettings: state => state.fbSettings,
  },
  mutations: {
    ...mixinDialogMutations,
    updateField,

    SET_ACTIVE_TAB: (state, tab) => {
      state.activeTab = tab;
      localStorage.setItem('settings-active-tab', JSON.stringify(tab));
    },

    SET_SETTINGS: (state, payload) => {
      state.settings = payload;
    },

    SET_FB_SETTINGS: (state, payload) => {
      state.fbSettings = payload;
    },

    SET_GLOBALS: (state, payload) => {
      state.globals = payload;
    },

    SET_OPTIONS: (state, payload) => {
      state.settings[payload.category][payload.option] = payload.value;
    },

    SET_OPTIONS_2: (state, payload) => {
      state[payload.category][payload.option] = payload.value;
    },

    SET_BINOM_DATA: (state, payload) => {
      state.binomData = payload;
    },

    SET_KEITARO_DATA: (state, payload) => {
      state.keitaroData = payload;
    }
  },
  actions: {
    ...mixinDialogActions,

    async setActiveTab(context, tab) {
      context.commit('SET_ACTIVE_TAB', tab);
    },

    async loadFbSettings({ commit }) {
      let response = await this._vm.api('/fb_settings');
      let settings = {
        sync: 0,
        interval: 0,
        auto_publish: 0,
      }

      if (response.status === 200 && response.data.data) {
        settings = response.data.data;
      }

      commit('SET_FB_SETTINGS', settings);
    },

    async loadSettings({commit}) {
      let response = await this._vm.api('/settings');
      if (typeof response.data.data != 'undefined') {
        let settings = response.data.data.settings;

        if (settings.general && typeof settings.general.send_debug_data === 'undefined') {
          settings.general.send_debug_data = false;
        }
        
        if (typeof settings.intervals === 'undefined') {
          settings.intervals = {
            // fp_auto_publish_time: 0,
            comments: 0,
            comments_time: 0,
            comments_mode: null,
          };
        }
        if (typeof settings.general === 'undefined') {
          settings.general = {
            change_ip_from_ui: false
          };
        }

        if (typeof settings.domains === 'undefined') {
          settings.domains = {
            cf_api_key: null
          };
        }

        if (settings.intervals && typeof settings.intervals.sync === 'undefined') {
          settings.intervals.sync = 0;
        }

        if (settings.intervals && typeof settings.intervals.sync_time === 'undefined') {
          settings.intervals.sync_time = 0;
        }

        if (settings.intervals && typeof settings.intervals.fp_auto_publish === 'undefined') {
          settings.intervals.fp_auto_publish = 0;
        }

        // if (typeof settings.intervals.fp_auto_publish_time === 'undefined') {
        //   settings.intervals.fp_auto_publish_time = 0;
        // }

        if (settings.intervals && typeof settings.intervals.comments === 'undefined') {
          settings.intervals.comments = 0;
        }

        if (settings.intervals && typeof settings.intervals.comments_time === 'undefined') {
          settings.intervals.comments_time = 0;
        }

        if (settings.intervals && typeof settings.intervals.comments_mode === 'undefined') {
          settings.intervals.comments_mode = 0;
        }

        if (settings.general && typeof settings.general.general_wait_before_change_ip === 'undefined') {
          settings.general.general_wait_before_change_ip = 30;
        }

        if (settings.general && typeof settings.general.general_wait_proxy_change_ip === 'undefined') {
          settings.general.general_wait_proxy_change_ip = 90;
        }

        if (settings.upload && typeof settings.upload.url_params === 'undefined') {
          settings.upload.url_params = '';
          settings.upload.url_params_target = 'link';
        }

        if (settings.upload && typeof settings.upload.deeplink_params === 'undefined') {
          settings.upload.deeplink_params = '';
        }

        if (typeof settings.notifications === 'undefined') {
          settings.notifications = {
            notifications_telegram: null,
            notifications_telegram_tokens: null,
            notifications_telegram_cabs: null,
            notifications_telegram_moderation: null,
            notifications_telegram_billing: null,
            notifications_telegram_billing_threshold: null,
            notifications_telegram_proxy_expirity: null,
            notifications_alarmerbot_key: null
          };
        }
        if (settings.costs && typeof settings.costs.costs_binom === 'undefined') {
          settings.costs.costs_binom = null;
        }
        if (settings.costs && typeof settings.costs.costs_binom_api_key === 'undefined') {
          settings.costs.costs_binom_api_key = null;
        }
        if (settings.costs && typeof settings.costs.costs_binom_interval === 'undefined') {
          settings.costs.costs_binom_interval = 60;
        }
        if (settings.costs && typeof settings.costs.costs_binom_object === 'undefined') {
          settings.costs.costs_binom_object = 'campaign';
        }
        if (settings.costs && typeof settings.costs.costs_binom_period === 'undefined') {
          settings.costs.costs_binom_period = 'today_and_yesterday';
        }
        if (settings.costs && typeof settings.costs.costs_binom_token === 'undefined') {
          settings.costs.costs_binom_token = 1;
        }
        if (settings.costs && typeof settings.costs.costs_binom_target === 'undefined') {
          settings.costs.costs_binom_target = 'all';
        }
        if (settings.costs && typeof settings.costs.costs_binom_timeout === 'undefined') {
          settings.costs.costs_binom_timeout = '250';
        }
        if (settings.costs && typeof settings.costs.costs_binom_url === 'undefined') {
          settings.costs.costs_binom_url = null;
        }
        if (settings.costs && typeof settings.costs.costs_binom_sources === 'undefined') {
          settings.costs.costs_binom_sources = null;
        }
        if (settings.costs && typeof settings.costs.costs_binom_groups === 'undefined') {
          settings.costs.costs_binom_groups = null;
        }
        if (settings.costs && typeof settings.costs.costs_binom_campaigns === 'undefined') {
          settings.costs.costs_binom_campaigns = null;
        }
        if (settings.costs && typeof settings.costs.costs_keitaro === 'undefined') {
          settings.costs.costs_keitaro = null;
        }
        if (settings.costs && typeof settings.costs.costs_keitaro_api_key === 'undefined') {
          settings.costs.costs_keitaro_api_key = null;
        }
        if (settings.costs && typeof settings.costs.costs_keitaro_interval === 'undefined') {
          settings.costs.costs_keitaro_interval = 60;
        }
        if (settings.costs && typeof settings.costs.costs_keitaro_object === 'undefined') {
          settings.costs.costs_keitaro_object = 'campaign';
        }
        if (settings.costs && typeof settings.costs.costs_keitaro_period === 'undefined') {
          settings.costs.costs_keitaro_period = 'today_and_yesterday';
        }
        if (settings.costs && typeof settings.costs.costs_keitaro_subid === 'undefined') {
          settings.costs.costs_keitaro_subid = 1;
        }
        if (settings.costs && typeof settings.costs.costs_keitaro_target === 'undefined') {
          settings.costs.costs_keitaro_target = 'all';
        }
        if (settings.costs && typeof settings.costs.costs_keitaro_timeout === 'undefined') {
          settings.costs.costs_keitaro_timeout = '250';
        }
        if (settings.costs && typeof settings.costs.costs_keitaro_timezones_reset === 'undefined') {
          settings.costs.costs_keitaro_timezones_reset = null;
        }
        if (settings.costs && typeof settings.costs.costs_keitaro_url === 'undefined') {
          settings.costs.costs_keitaro_url = null;
        }
        if (settings.costs && typeof settings.costs.costs_keitaro_sources === 'undefined') {
          settings.costs.costs_keitaro_sources = null;
        }
        if (settings.costs && typeof settings.costs.costs_keitaro_groups === 'undefined') {
          settings.costs.costs_keitaro_groups = null;
        }
        if (settings.costs && typeof settings.costs.costs_keitaro_campaigns === 'undefined') {
          settings.costs.costs_keitaro_campaigns = null;
        }
        if (typeof settings.costs === 'undefined') {
          settings.costs = {
            costs_binom: null,
            costs_binom_api_key: null,
            costs_binom_interval: 60,
            costs_binom_object: 'campaign',
            costs_binom_period: 'today_and_yesterday',
            costs_binom_token: 1,
            costs_binom_target: 'all',
            costs_binom_timeout: '250',
            costs_binom_url: null,
            costs_binom_sources: null,
            costs_binom_groups: null,
            costs_binom_campaigns: null,
            costs_keitaro: null,
            costs_keitaro_api_key: null,
            costs_keitaro_interval: 60,
            costs_keitaro_object: 'campaign',
            costs_keitaro_period: 'today_and_yesterday',
            costs_keitaro_subid: 1,
            costs_keitaro_target: 'all',
            costs_keitaro_timeout: '250',
            costs_keitaro_timezones_reset: null,
            costs_keitaro_url: null,
            costs_keitaro_sources: null,
            costs_keitaro_groups: null,
            costs_keitaro_campaigns: null
          };
        }
        if (typeof settings.comments === 'undefined') {
          settings.comments = {
            trigger_words_mode: null,
            trigger_words: null
          };
        }
        if (settings.domains && typeof settings.domains.cf_api_key === 'undefined') {
          settings.domains.cf_api_key = null;
        }
        // if (settings.domains && typeof settings.domains.cf_email === 'undefined') {
        //   settings.domains.cf_email = null;
        // }

        commit('SET_SETTINGS', settings);

        if (response.data.data && response.data.data.globals) {
          commit('SET_GLOBALS', response.data.data.globals);
        }
      }
    },

    async saveSettings({dispatch}, payload) {
      await this._vm.api.post('/settings/update', {settings: payload});
      dispatch('loadSettings');
    },

    setOptions(context, data) {
      context.commit('SET_OPTIONS', data);
    },

    setOptions2(context, data) {
      context.commit('SET_OPTIONS_2', data);
    },

    async setBinomData(context, data) {
      const response = await this._vm.api.post('/settings/binom_data', data).catch((e) => {
        context.dispatch('main/apiError', e, {
          root: true
        });
      });
      if (response.data.success) {
        context.commit('SET_BINOM_DATA', response.data.data);
      }
    },

    async setKeitaroData(context, data) {
      const response = await this._vm.api.post('/settings/keitaro_data', data).catch((e) => {
        context.dispatch('main/apiError', e, {
          root: true
        });
      });
      if (response.data.success) {
        context.commit('SET_KEITARO_DATA', response.data.data);
      }
    },
  },
};