import i18n                           from '../../i18n';
import {
  mixinDialogMutations,
  mixinDialogActions,
  mixinDialogGetters
} from '../../mixins/vuex_dialogs';

export default {
  namespaced: true,
  state: {
    dialogs: {
      filters: false,
      dialogAboutPauseBetweenAdAccounts: false,
      dialogWhyOnlyUsd: false,
      preview: false,
      mainStepperStepCreativesDialogAvailableSpintax: false,
      creatives: false,
      mainStepperStepCreativesTopbarCreoDialogCreo: false,
      dialogAvailableMacros: false
    },
    job: null,
    selectedBundle: null,
    cabs: {
      all: [],
      filtered: []
    },
    accounts: {
      all: [],
      filtered: []
    },
    emptyFilters: {
      accounts: [],
      readyForAdsCreation: false,
      onlyActiveAccounts: false,
      onlyActiveCabs: false,
      onlyWithAttachedCard: false,
      onlySelected: false,
      cabInput: '',
      accountTags: [],
      cabsTags: [],
      cabsSearch: '',
      withoutAds: false,
      withoutActiveCampaigns: false
    },
    filters: {
    },
    emptyFiltersSelectedCabs: {
      cabsSearch: '',
    },
    filtersSelectedCabs: {},
    cabsOptions: {},
    tags: {
      accounts: [],
      cabs: []
    },
    creativeForPreview: {},
    loading: {
      createAd: false,
      refreshCab: false,
      cabId: null
    },
    showWarnings: false,
    wizardOptions: {
      uploadWithNaming: false
    }
  },
  getters: {
    ...mixinDialogGetters,

    job: state => state.job,
    selectedBundle: state => state.selectedBundle,
    cabs: state => state.cabs,
    filters: state => state.filters,
    tags: state => state.tags,
    creativeForPreview: state => state.creativeForPreview,
    loading: state => state.loading,
    showWarnings: state => state.showWarnings,
    accounts: state => state.accounts,
  },
  mutations: {
    ...mixinDialogMutations,

    SET_BUNDLE: (state, data) => {
      state.selectedBundle = data;
    },

    SET_SELECTED_BUNDLE: (state, data) => {
      state.selectedBundle = data;
    },

    SET_JOB: (state, data) => {
      state.job = data;
    },

    SET_JOB_PROPERTY: (state, { key, value }) => {
      state.job[key] = value;
    },

    SET_CABS: (state, payload) => {
      payload.forEach(cab => {
        if (!state.cabs.all.find(cab2 => cab2.id === cab.id)) {
          state.cabs.all.push(cab);
        } else {
          state.cabs.all[state.cabs.all.findIndex(cab3 => cab3.id === cab.id)] = cab;
        }
      });
    },

    SET_ACCOUNTS: (state, payload) => {
      state.accounts.all = payload;
    },

    LOAD_FILTERS: (state) => {
      if (localStorage.getItem('upload-wizard-cabs-filters')) {
        state.filters = JSON.parse(localStorage.getItem('upload-wizard-cabs-filters'));
      } else {
        state.filters = {...state.emptyFilters};
      }
    },

    SET_SPECIFIC_FILTER: (state, data) => {
      state.filters[data.filter] = data.data;
      localStorage.setItem('upload-wizard-cabs-filters-selected-cabs', JSON.stringify(state.filters));
    },

    FILTER_CABS (state) {
      let cabs = state.cabs.all;
      const filters = state.filters;

      if (filters.accounts && filters.accounts.length > 0) {
        cabs = cabs.filter(cab => {
          // return filters.accounts.some(account => {
          //   return cab.account_id.toString().toLowerCase().indexOf(account.id.toString().toLowerCase()) > -1;
          // });
          return filters.accounts.find(account => account.id === cab.account_id);
        });
      }

      if (
        typeof filters.accountTags !== 'undefined' &&
        filters.accountTags &&
        filters.accountTags.length > 0
      ) {
        cabs = cabs.filter(cab => {
          return filters.accountTags.some(tag => {
            if (!Array.isArray(cab.account.tags)) return false;
            return cab.account.tags.indexOf(tag) > -1;
          });
        });
      }

      if (
        typeof filters.cabsTags !== 'undefined' &&
        filters.cabsTags &&
        filters.cabsTags.length > 0
      ) {
        cabs = cabs.filter(cab => {
          return filters.cabsTags.some(tag => {
            if (!Array.isArray(cab.tags)) return false;
            return cab.tags.indexOf(tag) > -1;
          });
        });
      }

      if (
        typeof state.filters.readyForAdsCreation !== 'undefined' &&
                state.filters.readyForAdsCreation
      ) {
        cabs = cabs.filter(cab => (
          cab.card_number !== null
          && cab.card_number.toString().length > 0
          && typeof cab.account !== 'undefined'
          && cab.account.status === 'ACTIVE'
          && cab.status === 1
          && cab.pages.length > 0
          && (state.job.objective === 'APP_INSTALLS' ? cab.apps.length > 0 : true)
        ));
      }

      if (filters.cabInput && filters.cabInput.length > 0) {
        cabs = cabs.filter(cab => cab.ads_stat.total < parseInt(filters.cabInput));
      }

      if (
        typeof state.filters.onlyActiveAccounts !== 'undefined' &&
                state.filters.onlyActiveAccounts
      ) {
        cabs = cabs.filter(cab => cab.account.status === 'ACTIVE');
      }

      if (
        typeof state.filters.onlyActiveCabs !== 'undefined' &&
                state.filters.onlyActiveCabs
      ) {
        cabs = cabs.filter(cab => cab.status === 1);
      }

      if (
        typeof state.filters.onlyWithAttachedCard !== 'undefined' &&
                state.filters.onlyWithAttachedCard
      ) {
        cabs = cabs.filter(cab => cab.card_number);
      }

      if (
        typeof state.filters.onlySelected !== 'undefined' &&
                state.filters.onlySelected
      ) {
        cabs = state.job.cabs.map(cab => cab);
      }

      if (filters.cabsSearch && filters.cabsSearch.toString().length > 0) {
        cabs = cabs.filter(cab => {
          if (cab.cab_id && cab.cab_id.toString().toLowerCase().search(filters.cabsSearch.toString().toLowerCase()) !== -1) {
            return true;
          }
          if (cab.business_name && cab.business_name.toString().toLowerCase().search(filters.cabsSearch.toString().toLowerCase()) !== -1) {
            return true;
          }
          if (cab.account.name && cab.account.name.toString().toLowerCase().search(filters.cabsSearch.toString().toLowerCase()) !== -1) {
            return true;
          }
          if (cab.name && cab.name.toString().toLowerCase().search(filters.cabsSearch.toString().toLowerCase()) !== -1) {
            return true;
          }
          return false;
        });
      }

      if (
        typeof state.filters.withoutAds !== 'undefined' &&
                state.filters.withoutAds
      ) {
        cabs = cabs.filter(cab => cab.ads_stat.total === 0);
      }

      if (state.filters.withoutActiveCampaigns !== undefined && state.filters.withoutActiveCampaigns) {
        cabs = cabs.filter(cab => !cab.has_active_campaigns);
      }
      state.cabs.filtered = cabs;
    },

    SET_TAGS: (state, data) => {
      state.tags[data.tags] = data.data;
    },

    REFRESH_CAB: (state, payload) => {
      let cabs = state.cabs.all.map(cab => {
        if (cab.id === payload.id) {
          if (payload.pages && payload.pages.length > 0) {
            state.cabsOptions[cab.id].page_id = payload.pages.find(page => page.is_published !== 0)
              ? payload.pages.find(page => page.is_published !== 0).id
              : null;
          }
          if (payload.apps && payload.apps.length > 0) {
            state.cabsOptions[cab.id].app_id = payload.apps[0].id;
          }
          return cab = payload;
        } else {
          return cab;
        }
      });

      state.cabs.all = cabs;
    },

    LOADING_REFRESH: (state, payload) => {
      state.loading.cabId = payload;
    },

    SET_LOADING: (state, data) => {
      state.loading[data.param] = data.value;
    },

    SET_WARNING: (state, payload) => {
      state.showWarnings = payload;
    },
  },

  actions: {
    ...mixinDialogActions,

    setBundle({ commit }, data) {
      commit('SET_BUNDLE', data);
    },

    setSelectedBundle({ commit }, data) {
      commit('SET_SELECTED_BUNDLE', data);
    },

    async loadCabs({commit}) {
      let data = {users_ids: -1, accounts_ids: -1};
      const response = await this._vm.api.post('/cabs', data);
      if (typeof response.data.data !== 'undefined') {
        let cabs = response.data.data.filter(cab => cab.archived === 0);
        commit('SET_CABS', cabs);
      }
      commit('FILTER_CABS');
    },

    async loadAccounts({commit}) {
      let data = {users_ids: -1};
      const response = await this._vm.api.post('/accounts', data);
      if (typeof response.data.data !== 'undefined') {
        let accounts = response.data.data.filter(account => account.archived === 0);
        commit('SET_ACCOUNTS', accounts);
      }
      // commit('FILTER_CABS');
    },

    async loadFilters(context) {
      context.commit('LOAD_FILTERS');
    },

    async setSpecificFilter(context, data) {
      context.commit('SET_SPECIFIC_FILTER', data);
      context.commit('FILTER_CABS');
    },

    async loadTags(context, payload) {
      const response = await this._vm.api(`/tags/?only_from=${payload}`);
      if (typeof response.data.data !== 'undefined') {
        let data = {
          tags: payload,
          data: response.data.data
        };
        context.commit('SET_TAGS', data);
      }
    },

    async refreshCab(context, payload) {
      await context.commit('LOADING_REFRESH', payload);
      context.commit('SET_LOADING', {
        param: 'refreshCab',
        value: true
      });
      const response = await this._vm.api.get(`/cabs/refresh/${payload}`);
      if (response.data.success) {
        let data = {
          cabs_ids: [payload]
        };
        const cabs = await this._vm.api.post('/cabs', data);
        if (cabs.data.success) {
          context.commit('SET_LOADING', {
            param: 'refreshCab',
            value: false
          });
          await context.commit('REFRESH_CAB', cabs.data.data[0]);

          context.dispatch('loadCabs');
        }
      }
    },

    async createAd(context) {
      let data = {...context.state.job};
      data.selected_cabs = context.state.job.cabs.map(cab => cab.id);
      data.cabs_options = {...context.state.cabsOptions};

      if (context.state.wizardOptions.uploadWithNaming) {
        for (const prop in data.cabs_options) {
          if (!data.cabs_options[prop].campaign_name) {
            data.cabs_options[prop].campaign_name = data.campaign_name;
          }
        }
      }

      delete data.cabs;
      delete data.global;
      delete data.pixel;

      data.apply_rules = data.apply_rules.map(rule => rule.id);
      data.apply_rules_groups = data.apply_rules_groups.map(group => group.id);

      context.commit('SET_LOADING', {
        param: 'createAd',
        value: true
      });

      const response = await this._vm.api.post('/jobs/create_new', data).catch((e) => {
        context.dispatch('main/apiError', e, {
          root: true
        });
      });

      context.commit('SET_LOADING', {
        param: 'createAd',
        value: false
      });

      if (response.data.success) {
        context.dispatch('main/alert', {
          color: 'success',
          message: i18n.t('common.adCreated')
        }, {
          root: true
        });
        context.dispatch('loadCabs');
      } else {
        context.dispatch('main/apiError', response.data, {
          root: true
        });
      }

      return response.data.success;
    },

    async setWarning(context, data) {
      context.commit('SET_WARNING', data);
    },

    async setJob({ commit }, data) {
      commit('SET_JOB', data);
    },

    async setJobProperty({ commit }, data) {
      commit('SET_JOB_PROPERTY', data);
    },
  },
};
