import {
  mixinDialogMutations,
  mixinDialogActions,
  mixinDialogGetters
} from '../../mixins/vuex_dialogs';

export default {
  namespaced: true,
  state: {
    profile: {
      columns: [],
      system_load: {
        disk: 0,
        memory: 0,
        cpu: 0,
      },
      days_left: 4,
      plan: 'base',
      currency:
          localStorage.getItem('adsmanager-currency') === null ||
          localStorage.getItem('adsmanager-currency') === 'undefined'
            ? 'USD'
            : localStorage.getItem('adsmanager-currency'),

      language: 
        localStorage.getItem('language') === null ||
        localStorage.getItem('language') === 'undefined'
          ? 'en'
          : localStorage.getItem('language'),
    },
    breadcrumbs: [],
    sidebarCollapsed: false,
    dialogs: {
      apiError: false,
      alert: false,
      feedback: false,
    },
    apiError: '',
    innerHeight: 0,
    alert: {
      color: '',
      message: '',
    },
    sidebar: false
  },
  getters: {
    ...mixinDialogGetters,

    breadcrumbs: state => {
      return state.breadcrumbs;
    },

    apiError: state => state.apiError,

    innerHeight: state => state.innerHeight,

    alert: state => state.alert,

    profile: state => state.profile,
    sidebar: state => state.sidebar
  },
  mutations: {
    ...mixinDialogMutations,

    setBreadcrumbs: ( state, breadcrumbs ) => {
      state.breadcrumbs = breadcrumbs;
    },

    toggleSidebar: ( state ) => {
      state.sidebarCollapsed = !state.sidebarCollapsed;
    },

    setUser: (state, user) => {
      state.user = {...user};
    },

    SET_API_ERROR: (state, error) => {
      state.apiError = error;
    },

    SET_INNER_HEIGHT: (state, height) => {
      state.innerHeight = height;
    },

    SET_ALERT: (state, alert) => {
      state.alert = alert;
    },

    OPEN_ALERT: (state) => {
      state.dialogs.alert = true;
    },

    CLOSE_ALERT: (state) => {
      state.dialogs.alert = false;
    },

    SET_PROFILE: (state, profile) => {
      state.profile = profile;
      state.profile.columns = profile.show_columns;

      if (!profile.language && localStorage.getItem('language')) {
        state.profile.language = localStorage.getItem('language');
      }
      if (!profile.theme_mode && localStorage.getItem('dark-mode')) {
        state.profile.theme_mode = localStorage.getItem('dark-mode') === 'true' ? 'dark' : 'light';
      }

      if(profile.show_columns) {
        state.profile.columns = profile.show_columns;
      } else {
        state.profile.columns = ['impressions'];
      }
      if (!Array.isArray(profile.columns_width)) {
        state.profile.columns_width = [];
      }
    },

    SET_SIDEBAR: (state, payload) => {
      state.sidebar = payload;
    }
  },

  actions: {
    ...mixinDialogActions,

    async loadProfile(context) {
      const response = await this._vm.api('/auth/profile');
      if (
        typeof response.data.errors !== 'undefined' &&
        response.data.errors[0] === 'auth error'
      ) {
        location.href = '/login';
      }
      if (typeof response.data.data !== 'undefined') {
        context.commit('SET_PROFILE', response.data.data);
      }

      return true;
    },

    async apiError(context, error) {
      console.log(error);
      context.commit('SET_API_ERROR', error);
      context.commit('OPEN_DIALOG', 'apiError');
    },

    async setInnerHeight(context, height) {
      context.commit('SET_INNER_HEIGHT', height);
    },

    async alert(context, alert) {
      context.commit('CLOSE_ALERT');
      context.commit('SET_ALERT', alert);
      context.commit('OPEN_ALERT');
      setTimeout(() => {
        context.commit('CLOSE_ALERT');
      }, 3000);
    },

    async closeAlert(context) {
      context.commit('CLOSE_ALERT');
    },

    async setSidebar(context, data) {
      context.commit('SET_SIDEBAR', data);
    }
  }
};
