export const mixinDialogMutations = {
  OPEN_DIALOG: (state, dialog) => {
    state.dialogs[dialog] = true;
  },

  CLOSE_DIALOG: (state, dialog) => {
    state.dialogs[dialog] = false;
  },

  ADD_DIALOG_RESPONSE: (state, {dialog, data}) => {
    state.dialogsResponse[dialog].response = data;
  },

  SET_DIALOG_PROCESSING_STATUS: (state, {dialog, status}) => {
    state.dialogsResponse[dialog].processing = status;
  }
};

export const mixinDialogActions = {
  async openDialog(context, dialog) {
    context.commit('OPEN_DIALOG', dialog);
  },

  async closeDialog(context, dialog) {
    context.commit('CLOSE_DIALOG', dialog);
  },

  async addDialogData(context, data) {
    context.commit('ADD_DIALOG_RESPONSE', data);
  },

  async setDialogProcessingStatus(context, data) {
    context.commit('SET_DIALOG_PROCESSING_STATUS', data);
  },
};

export const mixinDialogGetters = {
  dialogs: state => state.dialogs,
  dialogsResponse: state => state.dialogsResponse
};