import RestResource from '@/services/RestResource';

import i18n         from '../../i18n';
import {
  mixinDialogMutations,
  mixinDialogActions,
  mixinDialogGetters
} from '../../mixins/vuex_dialogs';
import {
  mixinSetLoading
} from '../../mixins/vuex_loading';

export default {
  namespaced: true,
  state: {
    ads: {
      all: [],
      selected: localStorage.getItem('adsmanager-ads-selected') ?
        JSON.parse(localStorage.getItem('adsmanager-ads-selected')) : [],
      filtered: [],
      forAdComments: {},
      forDialogMainInfo: {},
      count: 0,
      itemsCount: 0,
      page: 1
    },
    emptyFilters: {
      search: '',
      statuses: [],
      accountsStatuses: [],
      tags: [],
      onlySelected: false,
      withSpend: false
    },
    filters: {},
    dialogs: {
      pause: false,
      start: false,
      duplicate: false,
      filters: false,
      reviewFeedback: false,
      multipleAssignTags: false,
      comments: false,
      mainInfo: false,
      changeFp: false,
      appeal: false
    },
    loading: {
      mainTable: false,
      multipleAssignTags: false
    },
    stat: [],
    forReviewFeedback: null,
    sorts: {},
  },
  getters: {
    ...mixinDialogGetters,

    ads: state => state.ads,
    filters: state => state.filters,
    selected: state => state.ads.selected,
    count: state => state.ads.count,
    itemsCount: state => state.ads.itemsCount,
    page: state => state.ads.page,
    loading: state => state.loading,
    stat: state => state.stat,
    forReviewFeedback: state => state.forReviewFeedback,
    forAdComments: state => state.ads.forAdComments,
    columns: state => state.profile.columns,
  },
  mutations: {
    ...mixinDialogMutations,
    ...mixinSetLoading,

    FILTER: state => {
      state.ads.filtered = state.ads.all;
    },

    SORT: (state, data) => {
      state.sorts = {};

      if (data.key !== 'none') {
        state.sorts['key'] = data.key;
        state.sorts['value'] = data.value;
      }
    },

    COUNT: (state, data) => {
      state.ads.count = data;
    },

    ITEMS_COUNT: (state, data) => {
      state.ads.itemsCount = data;
    },

    SET_PAGE: (state, data) => {
      state.ads.page = data;
      localStorage.setItem('adsmanager-items-page', data);
    },

    SET_SPECIFIC_FILTER: (state, data) => {
      state.filters[data.filter] = data.data;
      localStorage.setItem('adsmanager-ads-filters', JSON.stringify(state.filters));
    },

    LOAD_FILTERS: (state) => {
      if (localStorage.getItem('adsmanager-ads-filters')) {
        state.filters = JSON.parse(localStorage.getItem('adsmanager-ads-filters'));
      } else {
        state.filters = {...state.emptyFilters};
      }
    },

    SET_STAT: (state, stat) => {
      state.stat = stat;
    },

    SET_SELECTED: (state, data) => {
      state.ads.selected = data;
      localStorage.setItem('adsmanager-ads-selected', JSON.stringify(data));
    },

    SET_ALL: (state, payload) => {
      state.ads.all = payload;
    },

    SET_FILTERED: (state, payload) => {
      state.ads.filtered = payload;
    },

    SET_LOADING: (state, data) => {
      state.loading[data.param] = data.value;
    },
    
    CLEAR_FILTERS: state => {
      state.filters = {
        ...state.emptyFilters
      };
      localStorage.removeItem('adsmanager-ads-filters');
    },

    SET_FOR_REVIEW_FEEDBACK: (state, payload) => {
      state.forReviewFeedback = payload;
    },

    SET_FOR_AD_COMMENTS: (state, data) => {
      state.ads.forAdComments = data;
    },

    SET_FOR_DIALOG_MAIN_INFO: (state, payload) => {
      state.ads.forDialogMainInfo = payload;
    },

    SET_CURRENCY: (state, data) => {
      state.filters.currency = data;
    },
  },
  actions: {
    ...mixinDialogActions,

    setPage({commit}, data) {
      commit('SET_PAGE', data);
    },

    async loadAds({state, commit, rootState}) {
      try {
        commit('SET_LOADING', {
          param: 'mainTable',
          value: true,
        });

        let url = '/fb-ads?';
        const restResourceService = new RestResource();

        let response = await restResourceService.sendRequest(state, rootState.main.profile.columns, url);

        if (response.status === 200) {
          commit('SET_ALL', response.data.data);
          commit('FILTER', response.data.data);
          commit('COUNT', response.data.meta.last_page);
          commit('ITEMS_COUNT', response.data.meta.total);
          commit('SET_PAGE', response.data.meta.current_page);
        }
      } catch (_) {
        //Do Noothing
      } finally {
        commit('SET_LOADING', {
          param: 'mainTable',
          value: false,
        });
      }
    },

    async saveSelected(context, data) {
      context.commit('SET_SELECTED', data);
    },

    async clearSelected(context) {
      context.commit('SET_SELECTED', []);
    },

    async clear(context) {
      context.commit('SET_ALL', []);
      context.commit('SET_FILTERED', []);
      context.commit('SET_SELECTED', []);
    },

    async setSpecificFilter(context, data) {
      context.commit('SET_SPECIFIC_FILTER', data);
      context.dispatch('filterAds');
      context.dispatch('loadAds');
    },

    async filterAds(context) {
      await context.dispatch('loadAds');
    },

    async sortAds(context, data) {
      context.commit('SORT', data);
      await context.dispatch('loadAds');
    },

    async loadFilters(context) {
      context.commit('LOAD_FILTERS');
    },

    async saveMultipleTags(context, data) {
      context.commit('SET_LOADING', {
        param: 'multipleAssignTags',
        value: true
      });

      const response = await this._vm.api.post('/ads/update_multiple_tags', data).catch((e) => {
        context.dispatch('main/apiError', e, {
          root: true
        });
      });

      context.commit('SET_LOADING', {
        param: 'multipleAssignTags',
        value: false
      });

      if (response.data.success) {
        context.dispatch('main/alert', {
          color: 'success',
          message: i18n.t('common.saved')
        }, {
          root: true
        });

        context.dispatch('loadAds');
        return true;
      } else {
        context.dispatch('main/apiError', response.data, {
          root: true
        });
      }

      return response.data.success;
    },

    async clearFilters(context) {
      await context.commit('CLEAR_FILTERS');
      context.dispatch('filterAds');
      context.dispatch('loadAds');
    },

    async setForReviewFeedback(context, data) {
      context.commit('SET_FOR_REVIEW_FEEDBACK', data);
    },

    async setForAdComments(context, data) {
      context.commit('SET_FOR_AD_COMMENTS', data);
    },

    async setForDialogMainInfo(context, data) {
      await context.commit('SET_FOR_DIALOG_MAIN_INFO', data);
    },

    async changeCurrency(context) {
      await context.dispatch('loadAds');
    },
  }
};
