import emptyBundle               from '@/constants/bundles/empty_bundle';
import { getField, updateField } from 'vuex-map-fields';

import i18n                      from '../../i18n';
import {
  mixinDialogMutations,
  mixinDialogActions,
  mixinDialogGetters
} from '../../mixins/vuex_dialogs';

export default {
  namespaced: true,
  state: {
    dialogs: {
      filters: false,
      unarchive: false,
      tags: false,
      multipleAssignTags: false,
      multipleShare: false,
      mainDialog: false,
      mainDialogAds: false,
      mainDialogAdsets: false,
      mainDialogCampaigns: false,
      mainDialogCampaignsDialogAvailableMacros: false,
      mainDialogCampaignsDialogAboutBundleBudget: false,
      mainDialogCampaignsDialogWhatIsBudgetRandomize: false,
      mainDialogCampaignsDialogAboutBidAmount: false,
      mainDialogAdsetsDialogAboutAgeRandomization: false,
      mainDialogAdsetsDialogAvailableMacrosForAdsets: false,
      mainDialogAdsetsDialogImportFromLists: false,
      mainDialogAdsDialogAvailableSpintax: false,
      mainDialogAdsDialogAvailableMacrosForAds: false
    },
    loading: {
      tags: false,
      multipleAssignTags: false,
      multipleShare: false,
      createOrSaveBundle: false
    },
    bundles: {
      all: [],
      filtered: [],
      selected: localStorage.getItem('upload-bundles-selected') ?
        JSON.parse(localStorage.getItem('upload-bundles-selected')) : [],
      forMainDialog: {}
    },
    emptyFilters: {
      search: '',
      tags: [],
      showArchived: false,
    },
    filters: localStorage.getItem('upload-bundles-filters') ? JSON.parse(localStorage.getItem('upload-bundles-filters')) : {
      search: '',
      tags: [],
      showArchived: false,
    },
    mainDialogMode: 'create',
    lastCreatedBundle: 0,
    list: {
      object: null,
      type: null
    },
    lists: []
  },
  getters: {
    ...mixinDialogGetters,
    getField,

    bundles: state => state.bundles,
    selected: state => state.bundles.selected,
    filters: state => state.filters,
    loading: state => state.loading,
    mainDialogMode: state => state.mainDialogMode,
    forMainDialog: state => state.bundles.forMainDialog,
    lastCreatedBundle: state => state.lastCreatedBundle,
    list: state => state.list,
    lists: state => state.lists,
  },
  mutations: {
    ...mixinDialogMutations,
    updateField,

    SET_ALL: (state, payload) => {
      state.bundles.all = payload;
    },

    FILTER: state => {
      state.bundles.filtered = state.bundles.all;
    },

    FILTER_BUNDLES (state) {
      let bundles = state.bundles.all;
      const filters = state.filters;

      if (filters.search) {
        if (filters.search.toString().length > 0) {
          bundles = bundles.filter(bundle => {
            if (bundle.name.toString().toLowerCase().indexOf(filters.search.toString().toLowerCase()) !== -1) {
              return true;
            }
            return false;
          });
        }
      }

      if (state.filters.tags && state.filters.tags.length > 0) {
        bundles = state.bundles.filter(bundle => {
          return filters.tags.some(tag => {
            if (!Array.isArray(bundle.tags)) return false;
            return bundle.tags.findIndex(item => item.toString() === tag.toString()) > -1;
          });
        });
      }

      if (
        typeof state.filters.showArchived === 'undefined' ||
                !state.filters.showArchived
      ) {
        bundles = bundles.filter(bundle => bundle.archived === 0);
      }

      state.bundles.filtered = bundles;
    },

    SET_SELECTED: (state, data) => {
      state.bundles.selected = data;
      localStorage.setItem('upload-bundles-selected', JSON.stringify(data));
    },

    SET_SPECIFIC_FILTER: (state, data) => {
      state.filters[data.filter] = data.data;
      localStorage.setItem('upload-bundles-filters', JSON.stringify(state.filters));
    },

    LOAD_FILTERS: (state) => {
      if (localStorage.getItem('upload-bundles-filters')) {
        state.filters = JSON.parse(localStorage.getItem('upload-bundles-filters'));
      } else {
        state.filters = {...state.emptyFilters};
      }
    },

    SET_LOADING: (state, data) => {
      state.loading[data.param] = data.value;
    },

    SET_FOR_MAIN_DIALOG: (state, data) => {
      state.bundles.forMainDialog = data;
    },

    SET_MAIN_DIALOG_MODE: (state, data) => {
      state.mainDialogMode = data;
    },

    LAST_CREATED_BUNDLE: (state, data) => {
      state.lastCreatedBundle = data;
    },

    SET_LISTS: (state, payload) => {
      state.lists = payload;
    },

    SET_LIST: (state, data) => {
      state.list.type = data.type;
      state.list.object = data.object;
    },

    SET_FOR_MAIN_DIALOG_PROPERTY: (state, { key, value }) => {
      state.bundles.forMainDialog[key] = value;
    },
  },
  actions: {
    ...mixinDialogActions,

    async loadBundles({commit}) {
      const response = await this._vm.api('/bundles');
      if (typeof response.data.data !== 'undefined') {
        commit('SET_ALL', response.data.data);
        commit('FILTER_BUNDLES');
      }
    },

    async setSelected(context, payload) {
      context.commit('SET_SELECTED', payload);
    },

    async clearSelected(context) {
      context.commit('SET_SELECTED', []);
    },

    async setSpecificFilter(context, data) {
      context.commit('SET_SPECIFIC_FILTER', data);
      context.commit('FILTER_BUNDLES');
    },

    async loadFilters(context) {
      context.commit('LOAD_FILTERS');
    },

    async multipleDuplicateBundles(context, payload) {
      const data = {
        ids: payload
      };
      const response = await this._vm.api.post('/bundles/duplicate', data);
      if (response.status === 200) {
        context.dispatch('main/alert', {
          color: 'success',
          message: i18n.t('common.duplicated')
        }, {
          root: true
        });

        context.dispatch('loadBundles');
        return true;
      } else {
        context.dispatch('main/apiError', response.data, {
          root: true
        });
      }
    },

    async multipleDeleteBundles(context, payload) {
      const data = {
        ids: payload
      };
      const response = await this._vm.api.post('/bundles/delete', data);
      if (response.status === 204) {
        context.dispatch('main/alert', {
          color: 'success',
          message: i18n.t('common.deleted')
        }, {
          root: true
        });

        context.dispatch('loadBundles');
        return true;
      } else {
        context.dispatch('main/apiError', response.data, {
          root: true
        });
      }
    },

    async multipleArchiveBundles(context, payload) {
      const data = {
        ids: payload
      };
      const response = await this._vm.api.post('/bundles/archive', data);
      if (response.status === 204) {
        context.dispatch('main/alert', {
          color: 'success',
          message: i18n.t('dialogs.successMessages.archived')
        }, {
          root: true
        });

        context.dispatch('loadBundles');
        return true;
      } else {
        context.dispatch('main/apiError', response.data, {
          root: true
        });
      }
    },

    async multipleUnarchiveAccounts(context, payload) {
      const data = {
        ids: payload
      };
      const response = await this._vm.api.post('/bundles/unarchive', data);
      if (response.status === 200) {
        context.dispatch('main/alert', {
          color: 'success',
          message: i18n.t('dialogs.successMessages.unarchived')
        }, {
          root: true
        });
        context.dispatch('loadBundles');
      }
    },
    
    async saveMultipleTags(context, data) {
      context.commit('SET_LOADING', {
        param: 'multipleAssignTags',
        value: true
      });

      const response = await this._vm.api.post('/bundles/update_multiple_tags', data).catch((e) => {
        context.dispatch('main/apiError', e, {
          root: true
        });
      });

      context.commit('SET_LOADING', {
        param: 'multipleAssignTags',
        value: false
      });

      if (response.status === 204) {
        context.dispatch('main/alert', {
          color: 'success',
          message: i18n.t('common.saved')
        }, {
          root: true
        });
        context.dispatch('loadBundles');
        return true;
      } else {
        context.dispatch('main/apiError', response.data, {
          root: true
        });
      }

      return response.status;
    },

    async saveMultiplePermissions(context, data) {
      context.commit('SET_LOADING', {
        param: 'multipleShare',
        value: true
      });

      const response = await this._vm.api.post('/bundles/update_multiple_permissions', data).catch((e) => {
        context.dispatch('main/apiError', e, {
          root: true
        });
      });

      context.commit('SET_LOADING', {
        param: 'multipleShare',
        value: false
      });

      if (response.status === 200) {
        context.dispatch('main/alert', {
          color: 'success',
          message: i18n.t('common.saved')
        }, {
          root: true
        });

        context.dispatch('loadBundles');
      } else {
        context.dispatch('main/apiError', response.data, {
          root: true
        });
      }

      return response.status;
    },

    setForMainDialog({ commit }, data = {}) {
      commit('SET_FOR_MAIN_DIALOG', Object.assign(this._vm.$_.cloneDeep(emptyBundle), data));
    },

    setMainDialogMode(context, data) {
      context.commit('SET_MAIN_DIALOG_MODE', data);
    },

    async createBundle(context, data) {
      context.commit('SET_LOADING', {
        param: 'createOrSaveBundle',
        value: true
      });

      const response = await this._vm.api.post('/bundles', data).catch((e) => {
        context.dispatch('main/apiError', e, {
          root: true
        });
      });

      context.commit('SET_LOADING', {
        param: 'createOrSaveBundle',
        value: false
      });

      if (response.status === 200) {
        context.dispatch('main/alert', {
          color: 'success',
          message: i18n.t('common.bundleCreated')
        }, {
          root: true
        });

        await context.dispatch('loadBundles');
        context.commit('LAST_CREATED_BUNDLE', response.data.bundle_id);
      } else {
        await context.dispatch('main/apiError', response.data, {
          root: true
        });
      }

      return response.status;
    },

    async loadLists({commit}, data) {
      const response = await this._vm.api(`/lists?type=${data}`);
      if (typeof response.data.data !== 'undefined') {
        commit('SET_LISTS', response.data.data);
      }
    },

    setList({commit}, data) {
      commit('SET_LIST', data);
    },

    async setForMainDialogProperty({ commit }, data) {
      commit('SET_FOR_MAIN_DIALOG_PROPERTY', data);
    },
  },
};
