import emptyAdAccountOptions from '@/constants/tiktok/upload/empty_ad_account_options';
import { 
  randomizeBudgetFunc,
  convertBudgetFunc } from '@/mixins/budget_mixin';
import { 
  mixinMainTableMutations,
  mixinMainTableActions,
  mixinMainTableGetters,
  mixinMainTableState } from '@/mixins/tiktok/common/vuex_main_table';
import { 
  mixinDialogMutations,
  mixinDialogActions,
  mixinDialogGetters } from '@/mixins/vuex_dialogs';
import { getField, updateField } from 'vuex-map-fields';

export default {
  namespaced: true,
  state: {
    ...mixinMainTableState,
    selectedBundle: null,
    uploadBundle: null,
    $validation: null,
    dialogs: {
      creativesForAll: false
    },
    selectedAdAccounts: [],
    adAccountOptions: {},
    filters: {
      onlySelected: false
    }
  },
  getters: {
    ...mixinMainTableGetters,
    ...mixinDialogGetters,
    getField,

    selectedBundle: state => state.selectedBundle,
    uploadBundle: state => state.uploadBundle,
    $validation: state => state.$validation,
    selectedAdAccounts: state => state.selectedAdAccounts,
    adAccountOptions: state => state.adAccountOptions,
    mainTableData: state => state.mainTableData,
    mainLoading: state => state.mainLoading,
    mainTablePagination: state => state.mainTablePagination,
    filters: state => state.filters,
  },
  mutations: {
    ...mixinMainTableMutations,
    ...mixinDialogMutations,
    updateField,

    SET_SELECTED_BUNDLE: (state, payload) => {
      state.selectedBundle = payload;
    },

    SET_UPLOAD_BUNDLE: (state, payload) => {
      state.uploadBundle = payload;
    },

    SET_UPLOAD_BUNDLE_PROPERTY: (state, payload) => {
      state.uploadBundle[payload.key] = payload.value;
    },

    SET_VALIDATION: (state, payload) => {
      state.$validation = payload;
    },

    ADD_AD_ACCOUNT_OPTION: (state, { id, options }) => {
      state.adAccountOptions = { ...state.adAccountOptions, [id]: options };
    },

    SET_AD_ACCOUNT_OPTION_PROPERTY: (state, { id, key, value }) => {
      state.adAccountOptions[id][key] = value;
    },

    ADD_AD_ACCOUNT_OPTION_CREO: (state, { id, value }) => {
      state.adAccountOptions[id].creatives.push(value);
    },

    REMOVE_AD_ACCOUNT_OPTION_CREO: (state, { id, value }) => {
      state.adAccountOptions[id].creatives = state.adAccountOptions[id].creatives.filter(item => {
        return item.name !== value.name && item.size !== value.size;
      });
    },

    REMOVE_CREATIVE: (state, { id, creativeName }) => {
      state.adAccountOptions[id].creatives = state.adAccountOptions[id].creatives.filter(item => item.filename !== creativeName);
    },
  },
  actions: {
    ...mixinDialogActions,
    ...mixinMainTableActions,

    async setSelectedBundle({ commit }, data) {
      commit('SET_SELECTED_BUNDLE', data);
    },

    async setUploadBundle({ commit }, data) {
      commit('SET_UPLOAD_BUNDLE', data);
    },
    async setUploadBundleProperty({ commit }, data) {
      commit('SET_UPLOAD_BUNDLE_PROPERTY', data);
    },

    async setValidation({ commit }, data) {
      commit('SET_VALIDATION', data);
    },

    async addAdAccountOption({ commit, state }, { id, adAccount }) {
      const options = this._vm.$_.cloneDeep(emptyAdAccountOptions);
      const { pixels, apps, currency } = adAccount;
      const {
        budget,
        budgetRandomize,
        budgetRandomValue,
        budgetLevel,
        callToAction,
        text,
        objective,
        bid,
        optimizationGoal } = state.selectedBundle;

      if (objective === 'CONVERSIONS' && Array.isArray(pixels) && pixels.length) {
        for (const pixel of pixels) {
          if (parseInt(pixel.activity_status, 10) === 2) {
            options.pixel = pixel;
            break;
          }
        }
        if (options.pixel === null) {
          options.pixel = pixels[0];
        }

        const { events } = options.pixel;
        if (Array.isArray(events) && events.length) {
          for (const event of events) {
            if (parseInt(event.activity_status, 10) === 2) {
              options.pixelEvent = event;
              break;
            }
            if (options.pixelEvent === null) {
              options.pixelEvent = events[0];
            }
          }
        }
      }
      if (objective === 'APP_INSTALLS' && Array.isArray(apps) && apps.length) {
        options.app = apps[0];
      }

      if (budgetRandomize) {
        options.budget = randomizeBudgetFunc(budget, budgetRandomValue, budgetLevel, currency);
      } else {
        options.budget = convertBudgetFunc(budget, currency);
      }
      if (bid) {
        options.bid = convertBudgetFunc(bid, currency);
      }
      options.callToAction = callToAction;
      options.text = text;

      if (objective === 'APP_INSTALLS' && optimizationGoal === 'in_app_event') {
        options.appEvent = '';
      }

      commit('ADD_AD_ACCOUNT_OPTION', { id, options });
    },

    async setAdAccountOptionProperty({ commit }, data) {
      commit('SET_AD_ACCOUNT_OPTION_PROPERTY', data);
    },

    async addAdAccountOptionCreo({ commit }, data) {
      commit('ADD_AD_ACCOUNT_OPTION_CREO', data);
    },
    
    async removeAdAccountOptionCreo({ commit }, data) {
      commit('REMOVE_AD_ACCOUNT_OPTION_CREO', data);
    },
    async removeCreative({ commit }, data) {
      commit('REMOVE_CREATIVE', data);
    },
  }
};
