export default {
  namespaced: true,
  state: {
    tags: {
      all: []
    },
    currentLevelTags: {
      all: []
    },
    loading: false,
  },
  getters: {
    tags: state => state.tags,
    currentLevelTags: state => state.currentLevelTags,
    loading: state => state.loading,
  },
  mutations: {
    SET_TAGS: (state, tags) => {
      state.tags.all = tags;
    },

    SET_CURRENT_LEVEL_TAGS: (state, tags) => {
      state.currentLevelTags.all = tags;
    },

    SET_LOADING: (state, value) => {
      state.loading = value;
    }
  },
  actions: {
    async loadTags({ dispatch, commit }) {
      try {
        await dispatch('setLoading', true);
        
        const response = await this._vm.cloudApi('users/tags');
        commit('SET_TAGS', response.data.data);
      } catch (error) {
        // 
      } finally {
        dispatch('setLoading', false);
      }
    },

    async setLoading({ commit }, value) {
      commit('SET_LOADING', value);
    },

    async loadCurrentLevelTags(context, payload) {
      const response = await this._vm.api(`/tags/?only_from=${payload}`);
      if (typeof response.data.data !== 'undefined') {
        context.commit('SET_CURRENT_LEVEL_TAGS', response.data.data);
      }
    },

    //
    // Это наиболее совершенная на данный момент версия загрузки тегов =)
    //
    async loadItems({commit}, params) {
      const esc = encodeURIComponent;

      const query = Object.keys(params)
        .map(k => esc(k) + '=' + esc(params[k]))
        .join('&');

      const url = `/tags?${query}`;

      const response = await this._vm.api(url);
      if (typeof response.data.data !== 'undefined') {
        if (params.only_from) {
          commit('SET_CURRENT_LEVEL_TAGS', response.data.data);
        } else {
          commit('SET_TAGS', response.data.data);
        }
      }
    }
  }
};