export default {
  namespaced: true,
  state: {
    news: {},
    unreadNewsCount: {}
  },

  getters: {
    news: state => state.news,
    unreadNewsCount: state => state.unreadNewsCount
  },

  mutations: {
    LOAD_UNREAD_NEWS_COUNT: (state, payload) => {
      state.unreadNewsCount = payload;
    },

    LOAD_NEWS: (state, payload) => {
      state.news = payload;
    },

    LOAD_MORE: (state, payload) => {
      state.news.data = state.news.data.concat(payload.data);
      state.news.next = payload.next;
    }
  },

  actions: {
    async loadUnreadNewsCount(context, data) {
      const response = await this._vm.api.get(`https://panel.dolphin.ru.com/?c=news&m=unread_count&last_news=${data}`);
      context.commit('LOAD_UNREAD_NEWS_COUNT', response.data.data);
    },

    async loadNews(context, data) {
      const response = await this._vm.api.get(`https://panel.dolphin.ru.com/?c=news&m=feed&lang=${data.language}&lic=${data.lic}&dolphin_user_id=${data.id}`);
      context.commit('LOAD_NEWS', response.data);
      if (response.data.data && response.data.data.length > 0) {
        let lastNewsId = Math.max.apply(Math, response.data.data.map(function(news) { return parseInt(news.id); }));
        await this._vm.api.post('/profile/save_last_news', {news_id: lastNewsId});
        context.dispatch('main/loadProfile', null, {
          root: true
        });
        context.dispatch('loadUnreadNewsCount', lastNewsId);
      }
    },

    async loadMore(context, data) {
      const response = await this._vm.api.get(`${data}`);
      context.commit('LOAD_MORE', response.data);
    },
  }
};
