import RestResource from '@/services/RestResource';

import i18n         from '../../i18n';
import {
  mixinDialogMutations,
  mixinDialogActions,
  mixinDialogGetters
} from '../../mixins/vuex_dialogs';
import {
  mixinSetLoading
} from '../../mixins/vuex_loading';

export default {
  namespaced: true,
  state: {
    campaigns: {
      all: [],
      selected: localStorage.getItem('adsmanager-campaigns-selected') ?
        JSON.parse(localStorage.getItem('adsmanager-campaigns-selected')) : [],
      filtered: [],
      forDialogMainInfo: {},
      count: 0,
      itemsCount: 0,
      page: 1
    },
    emptyFilters: {
      search: '',
      statuses: [],
      cabsStatuses: [],
      accountsStatuses: [],
      tags: [],
      onlySelected: false,
      showArchived: false,
      withSpend: false,
      showDeleted: false
    },
    filters: {},
    dialogs: {
      changeBudget: false,
      pause: false,
      start: false,
      duplicate: false,
      filters: false,
      multipleAssignTags: false,
      deleteFromFb: false,
      mainInfo: false
    },
    loading: {
      mainTable: false,
      multipleAssignTags: false
    },
    stat: [],
    deleteFromFbLoading: {
      stopRequest: false,
      requestStatus: false,
      loading: false,
      done: 0,
      source: [],
      campaigns: []
    },
    sorts: {},
  },
  getters: {
    ...mixinDialogGetters,

    campaigns: state => state.campaigns,
    filters: state => state.filters,
    selected: state => state.campaigns.selected,
    loading: state => state.loading,
    count: state => state.adsets.count,
    itemsCount: state => state.adsets.itemsCount,
    page: state => state.adsets.page,
    stat: state => state.stat,
    deleteFromFbLoading: state => state.deleteFromFbLoading
  },
  mutations: {
    ...mixinDialogMutations,
    ...mixinSetLoading,

    FILTER: state => {
      state.campaigns.filtered = state.campaigns.all;
    },

    SET_SPECIFIC_FILTER: (state, data) => {
      state.filters[data.filter] = data.data;
      localStorage.setItem('adsmanager-campaigns-filters', JSON.stringify(state.filters));
    },

    LOAD_FILTERS: (state) => {
      if (localStorage.getItem('adsmanager-campaigns-filters')) {
        state.filters = JSON.parse(localStorage.getItem('adsmanager-campaigns-filters'));
      } else {
        state.filters = {...state.emptyFilters};
      }
    },

    SORT: (state, data) => {
      state.sorts = {};

      if (data.key !== 'none') {
        state.sorts['key'] = data.key;
        state.sorts['value'] = data.value;
      }
    },

    SET_DATE_FROM_DATE_TO: (state, data) => {
      state.filters.dateFrom = data.startDate;
      localStorage.setItem('fb-campaigns-filter-date-from', data.startDate);
      state.filters.dateTo = data.endDate;
      localStorage.setItem('fb-campaigns-filter-date-to', data.endDate);
    },

    COUNT: (state, data) => {
      state.adsets.count = data;
    },

    ITEMS_COUNT: (state, data) => {
      state.adsets.itemsCount = data;
    },

    SET_PAGE: (state, data) => {
      state.adsets.page = data;
      localStorage.setItem('adsmanager-items-page', data);
    },

    FILTER_CAMPAIGNS: (state, payload) => {
      let campaigns = state.campaigns.all;
      const filters = state.filters;
      const accountsFilters = localStorage.getItem('adsmanager-accounts-filters') ? JSON.parse(localStorage.getItem('adsmanager-accounts-filters')) : payload.rootState.accounts.filters;
      const cabsFilters = localStorage.getItem('adsmanager-cabs-filters') ? JSON.parse(localStorage.getItem('adsmanager-cabs-filters')) : payload.rootState.cabs.filters;
      const $_ = payload.$_;

      if ($_.isEmpty(accountsFilters) || !accountsFilters.showArchived) {
        campaigns = campaigns.filter(campaign => campaign.account.archived === 0);
      }
      if ($_.isEmpty(cabsFilters) || !cabsFilters.showArchived) {
        campaigns = campaigns.filter(campaign => campaign.cab.archived === 0);
      }

      if (filters.statuses && filters.statuses.length > 0) {
        campaigns = campaigns.filter(campaign => {
          return filters.statuses.includes(campaign.effective_status);
        });
      }

      if (filters.search) {
        if (filters.search.toString().length > 0) {
          campaigns = campaigns.filter(campaign => {
            if (campaign.campaign_id.toString().toLowerCase().indexOf(filters.search.toString().toLowerCase()) !== -1) {
              return true;
            }
            if (campaign.name.toString().toLowerCase().indexOf(filters.search.toString().toLowerCase()) !== -1) {
              return true;
            }
            return false;
          });
        }
      }

      if (filters.accountsStatuses && filters.accountsStatuses.length > 0) {
        campaigns = campaigns.filter(campaign => {
          return filters.accountsStatuses.includes(campaign.account.status);
        });
      }

      if (filters.cabsStatuses && filters.cabsStatuses.length > 0) {
        campaigns = campaigns.filter(campaign => {
          return filters.cabsStatuses.includes(campaign.cab.status);
        });
      }

      if (filters.tags && filters.tags.length > 0) {
        campaigns = campaigns.filter(campaign => {
          return filters.tags.some(tag => {
            if (!Array.isArray(campaign.tags)) return false;
            return campaign.tags.findIndex(item => item.toString() === tag.toString()) > -1;
          });
        });
      }
      
      if (
        typeof filters.onlySelected !== 'undefined' &&
          filters.onlySelected
      ) {
        campaigns = state.campaigns.selected.map(campaign => campaign);
      }

      if (
        typeof filters.showArchived === 'undefined' ||
          !filters.showArchived
      ) {
        campaigns = campaigns.filter(campaign => campaign.archived === 0);
      }
      if (
        typeof filters.showDeleted === 'undefined' ||
          !filters.showDeleted
      ) {
        campaigns = campaigns.filter(campaign => campaign.deleted_in_fb === 0);
      }

      if (
        typeof filters.withSpend !== 'undefined' &&
          filters.withSpend
      ) {
        campaigns = campaigns.filter(campaign => {
          for (let stat in state.stat) {
            if (campaign.id === parseInt(stat)) {
              return state.stat[stat].spend > 0;
            }
          }
        });
      }

      // if (payload && payload.users) {
      //   campaigns = campaigns.filter(campaign => payload.users.includes(campaign.user_id));
      // }
      // if (payload && payload.accounts) {
      //   campaigns = campaigns.filter(campaign => payload.accounts.includes(campaign.account.id));
      // }
      // if (payload && payload.cabs) {
      //   campaigns = campaigns.filter(campaign => payload.cabs.includes(campaign.cab_id));
      // }
      // if (payload && payload.adsets) {
      //   campaigns = campaigns.filter(campaign => payload.adsets.includes(campaign.campaign_id));
      // }
      // if (payload && payload.ads) {
      //   campaigns = campaigns.filter(campaign => payload.ads.includes(campaign.campaign_id));
      // }
      
      state.campaigns.filtered = campaigns;
    },

    SET_STAT: (state, stat) => {
      state.stat = stat;
    },

    SET_SELECTED: (state, data) => {
      state.campaigns.selected = data;
      localStorage.setItem('adsmanager-campaigns-selected', JSON.stringify(data));
    },

    SET_ALL: (state, payload) => {
      state.campaigns.all = payload;
    },

    SET_FILTERED: (state, payload) => {
      state.campaigns.filtered = payload;
    },

    CLEAR_FILTERS: state => {
      state.filters = {
        ...state.emptyFilters
      };
      localStorage.removeItem('adsmanager-campaigns-filters');
    },

    SET_LOADING_OPTIONS: (state, payload) => {
      state[payload.dialog][payload.option] = payload.value;
    },

    SET_LOADING_SOURCE: (state, payload) => {
      state[payload.dialog].source[payload.index] = payload.source;
    },

    SET_FOR_DIALOG_MAIN_INFO: (state, payload) => {
      state.campaigns.forDialogMainInfo = payload;
    },

  },
  actions: {
    ...mixinDialogActions,

    setPage({commit}, data) {
      commit('SET_PAGE', data);
    },

    async loadCampaigns({state, commit, rootState}) {
      try {
        commit('SET_LOADING', {
          param: 'mainTable',
          value: true,
        });
        let url = '/fb-campaigns?';
        const restResourceService = new RestResource();

        let response = await restResourceService.sendRequest(state, rootState.main.profile.columns, url);

        if (response.status === 200) {
          commit('SET_ALL', response.data.data);
          commit('FILTER', response.data.data);
          commit('COUNT', response.data.meta.last_page);
          commit('ITEMS_COUNT', response.data.meta.total);
          commit('SET_PAGE', response.data.meta.current_page);
        }
      } catch (_) {
        //Do Nothing
      } finally {
        commit('SET_LOADING', {
          param: 'mainTable',
          value: false,
        });
      }

    },

    async saveSelected(context, data) {
      context.commit('SET_SELECTED', data);
    },

    async clearSelected(context) {
      context.commit('SET_SELECTED', []);
    },

    async clear(context) {
      context.commit('SET_ALL', []);
      context.commit('SET_FILTERED', []);
      context.commit('SET_SELECTED', []);
    },

    async setSpecificFilter(context, data) {
      context.commit('SET_SPECIFIC_FILTER', data);
      context.dispatch('filterCampaigns');
      context.dispatch('loadCampaigns');
    },

    async filterCampaigns(context) {
      await context.dispatch('loadCampaigns');
    },

    async sortCampaigns(context, data) {
      context.commit('SORT', data);
      await context.dispatch('loadCampaigns');
    },

    async loadFilters(context) {
      context.commit('LOAD_FILTERS');
    },

    async saveMultipleTags(context, data) {
      context.commit('SET_LOADING', {
        param: 'multipleAssignTags',
        value: true
      });

      const response = await this._vm.api.post('/campaigns/update_multiple_tags', data).catch((e) => {
        context.dispatch('main/apiError', e, {
          root: true
        });
      });

      context.commit('SET_LOADING', {
        param: 'multipleAssignTags',
        value: false
      });

      if (response.data.success) {
        context.dispatch('main/alert', {
          color: 'success',
          message: i18n.t('common.saved')
        }, {
          root: true
        });

        context.dispatch('loadCampaigns');
        return true;
      } else {
        context.dispatch('main/apiError', response.data, {
          root: true
        });
      }

      return response.data.success;
    },

    async clearFilters(context) {
      await context.commit('CLEAR_FILTERS');
      context.dispatch('filterCampaigns');
      context.dispatch('loadCampaigns');
    },

    async archive(context, data) {
      const response = await this._vm.api.post('/campaigns/archive', data).catch((e) => {
        context.dispatch('main/apiError', e, {
          root: true
        });
      });
      if (response.data.success) {
        context.dispatch('main/alert', {
          color: 'success',
          message: i18n.t('common.archived')
        }, {
          root: true
        });
        context.dispatch('loadCampaigns');
        context.commit('SET_SELECTED', []);
      } else {
        context.dispatch('main/apiError', response.data, {
          root: true
        });
      }

      return response.data.success;
    },

    async unarchive(context, data) {
      const response = await this._vm.api.post('/campaigns/unarchive', data).catch((e) => {
        context.dispatch('main/apiError', e, {
          root: true
        });
      });
      if (response.data.success) {
        context.dispatch('main/alert', {
          color: 'success',
          message: i18n.t('common.unarchived')
        }, {
          root: true
        });
        context.dispatch('loadCampaigns');
        context.commit('SET_SELECTED', []);
      } else {
        context.dispatch('main/apiError', response.data, {
          root: true
        });
      }

      return response.data.success;
    },

    async setLoadingOptions({commit}, payload) {
      commit('SET_LOADING_OPTIONS', payload);
    },

    async setLoadingSource({commit}, payload) {
      commit('SET_LOADING_SOURCE', payload);
    },

    async setForDialogMainInfo(context, data) {
      await context.commit('SET_FOR_DIALOG_MAIN_INFO', data);
    },

    async changeCurrency(context) {
      context.dispatch('loadCampaigns');
    },
  }
};
