import {
  mixinDialogMutations,
  mixinDialogActions,
  mixinDialogGetters
} from '../../mixins/vuex_dialogs';

export default {
  namespaced: true,
  state: {
    activeTab: localStorage.getItem('bank-cards-active-tab') ?
      JSON.parse(localStorage.getItem('bank-cards-active-tab')) :
      {
        tab: 0,
        component: 'bank-cards-tab'
      },
    dialogs: {
      acceptCardsRules: false
    },
    rulesAccepted: false
  },
  getters: {
    ...mixinDialogGetters,

    activeTab: (state) => state.activeTab,
    rulesAccepted: (state) => state.rulesAccepted
  },
  
  mutations: {
    ...mixinDialogMutations,

    SET_ACTIVE_TAB: (state, tab) => {
      state.activeTab = tab;
      localStorage.setItem('bank-cards-active-tab', JSON.stringify(tab));
    },

    SET_RULES_ACCEPTED: (state, payload) => {
      state.rulesAccepted = payload;
    }
  },

  actions: {
    ...mixinDialogActions,

    async setActiveTab(context, tab) {
      context.commit('SET_ACTIVE_TAB', tab);
    },

    async acceptBankCardsRules(context) {
      const response = await this._vm.api.get('/profile/accept_bank_cards_rules');
      if (response.data.success) {
        context.dispatch('closeDialog', 'acceptCardsRules');
      } 
    },

    async setRulesAccepted(context, data) {
      context.commit('SET_RULES_ACCEPTED', data);
    }
  },
};