export default {
  namespaced: true,
  state: {
    rules: {
      all: [],
      groups: {
        all: []
      }
    }
  },
  getters: {
    rules: state => state.rules,
  },
  mutations: {
    SET_RULES: (state, data) => {
      state.rules.all = data;
    },

    SET_RULES_GROUP: (state, data) => {
      state.rules.groups.all = data;
    }
  },
  actions: {  
    async loadRules({commit}) {
      const response = await this._vm.api.get('/fb_rules');
      commit('SET_RULES', response.data);
    },

    async loadRulesGroup({commit}) {
      const response = await this._vm.api.get('/fb_rules/groups');
      commit('SET_RULES_GROUP', response.data.data);
    },
  },
};
