import emptyAccount from '@/constants/tiktok/adsmanager/accounts/empty_account';
import {
  mixinMainTableMutations,
  mixinMainTableActions,
  mixinMainTableGetters,
  mixinMainTableState } from '@/mixins/tiktok/common/vuex_main_table';
import {
  mixinDialogMutations,
  mixinDialogActions,
  mixinDialogGetters } from '@/mixins/vuex_dialogs';
import { getField, updateField } from 'vuex-map-fields';

export default {
  namespaced: true,
  state: {
    ...mixinMainTableState,
    dialogs: {
      create: false,
      edit: false,
      bulkDeleteConfirm: false,
      howToGetToken: false,
    },
    singleAccountData: null,
    $validation: null,
    selected: [],
    loading: {
      mainTable: false,
      addDialog: false,
      assignTagsDialog: false,
      share: false,
      multipleShare: false,
      editDialog: false,
      multipleAssignTags: false,
      moveAccounts: false,
      createBulkAccounts: false,
      topbarBulkDelete: false
    },
    checkTokenLoading: {
      stopRequest: false,
      requestStatus: false,
      loading: false,
      done: 0,
      source: [],
      accounts: []
    },
    sorts: [],
  },

  getters: {
    ...mixinDialogGetters,
    ...mixinMainTableGetters,
    getField,

    singleAccountData: state => state.singleAccountData,
    $validation: state => state.$validation,
    selected: state => state.selected,
    loading: state => state.loading,
    accounts: state => state.accounts,
    checkTokenLoading: state => state.checkTokenLoading,
  },
  
  mutations: {
    ...mixinDialogMutations,
    ...mixinMainTableMutations,
    updateField,

    SET_SINGLE_ACCOUNT_DATA: (state, payload) => {
      state.singleAccountData = payload;
    },

    SET_SINGLE_ACCOUNT_DATA_PROPERTY: (state, payload) => {
      state.singleAccountData[payload.key] = payload.value;
    },

    SET_VALIDATION: (state, payload) => {
      state.$validation = payload;
    },

    SET_LOADING_SOURCE: (state, payload) => {
      state[payload.dialog].source[payload.index] = payload.source;
    },

    SET_LOADING_OPTIONS: (state, payload) => {
      state[payload.dialog][payload.option] = payload.value;
    },

    SET_LOADING_ACCOUNTS: (state) => {
      state.checkTokenLoading.accounts = state.selected;
    },

    CLEAR_SELECTED: (state) => {
      state.selected = [];
    },
  },
  actions: {
    ...mixinDialogActions,
    ...mixinMainTableActions,

    async setSingleAccountData({ commit }, data) {
      const account = this._vm.$_.cloneDeep(emptyAccount);

      if (data) {
        account.id = data.id;
        account.name = data.name && typeof data.name === 'string' ?
          data.name : account.name;
        account.accessToken = data.access_token && typeof data.access_token === 'string' ?
          data.access_token : account.accessToken;
        account.proxyId = data.proxy_id ? data.proxy_id : account.proxyId;
      }

      commit('SET_SINGLE_ACCOUNT_DATA', account);
    },

    async setLoadingSource({commit}, payload) {
      commit('SET_LOADING_SOURCE', payload);
    },

    async setLoadingOptions({commit}, payload) {
      commit('SET_LOADING_OPTIONS', payload);
    },

    async setLoadingAccounts({commit}) {
      commit('SET_LOADING_ACCOUNTS');
    },

    async clearSelected({commit}) {
      commit('CLEAR_SELECTED');
    },

    async clearSingleAccountData({ commit }) {
      commit('SET_SINGLE_ACCOUNT_DATA', null);
    },

    async setSingleAccountDataProperty({ commit }, data) {
      commit('SET_SINGLE_ACCOUNT_DATA_PROPERTY', data);
    },

    async setValidation({ commit }, data) {
      commit('SET_VALIDATION', data);
    },

    async sortAccounts(context, data) {
      context.commit('SORT', data);
      const requestData = {
        endpoint: 'tiktok/accounts',
        orderBy: data.key,
        orderType: data.value
      };
  
      await context.dispatch('loadMainTableData', requestData);
    },
  }
};
