import { 
  mixinMainTableMutations,
  mixinMainTableActions,
  mixinMainTableGetters,
  mixinMainTableState } from '@/mixins/tiktok/common/vuex_main_table';
import { 
  mixinDialogMutations,
  mixinDialogActions,
  mixinDialogGetters } from '@/mixins/vuex_dialogs';
import { getField, updateField } from 'vuex-map-fields';

export default {
  namespaced: true,
  state: {
    ...mixinMainTableState,

    dialogs: {
      bulkDelete: false,
    },
    selectedJobs: [],
    restartedJobs: [],
  },
  getters: {
    ...mixinMainTableGetters,
    ...mixinDialogGetters,
    getField,

    selectedJobs: state => state.selectedJobs,
    restartedJobs: state => state.restartedJobs,
  },
  mutations: {
    ...mixinMainTableMutations,
    ...mixinDialogMutations,
    updateField,

    ADD_RESTARTED_JOB: (state, id) => {
      if (state.restartedJobs.indexOf(id) === -1) {
        state.restartedJobs.push(id);
      }
    },
    REMOVE_RESTARTED_JOB: (state, id) => {
      state.restartedJobs = state.restartedJobs.filter(item => item !== id);
    },

    REMOVE_SELECTED_JOB: (state, id) => {
      state.selectedJobs = state.selectedJobs.filter(item => item.id !== id);
    },

    EDIT_MAIN_TABLE_JOB: (state, { index, newJob }) => {
      state.mainTableData.splice(index, 1, newJob);
    },

    CLEAR_SELECTED_JOBS: (state) => {
      state.selectedJobs = [];
    },
  },
  actions: {
    ...mixinMainTableActions,
    ...mixinDialogActions,

    async addRestartedJob({ commit }, id) {
      commit('ADD_RESTARTED_JOB', id);
    },
    async removeRestartedJob({ commit }, id) {
      commit('REMOVE_RESTARTED_JOB', id);
    },
    async removeSelectedJob({ commit }, id) {
      commit('REMOVE_SELECTED_JOB', id);
    },
    async clearSelectedJobs({ commit }) {
      commit('CLEAR_SELECTED_JOBS');
    },
    async editMainTableJob({ commit, state }, data) {
      let oldJob = null;
      let oldJobIndex = null;
      const newData = {
        has_error: data.has_error,
        success: data.success,
        stage: data.stage,
      };

      state.mainTableData.forEach((job, jobIndex) => {
        if (job.id === data.id) {
          oldJob = job;
          oldJobIndex = jobIndex;
          return;
        }
      });

      if (oldJob && oldJobIndex !== null) {
        const newJob = Object.assign(this._vm.$_.cloneDeep(oldJob), newData);
        commit('EDIT_MAIN_TABLE_JOB', { index: oldJobIndex, newJob });
      }
    },
  }
};
