import RestResource from '@/services/RestResource';

import i18n         from '../../i18n';
import {
  mixinDialogMutations,
  mixinDialogActions,
  mixinDialogGetters
} from '../../mixins/vuex_dialogs';
import {
  mixinSetLoading
} from '../../mixins/vuex_loading';

export default {
  namespaced: true,
  state: {
    adsets: {
      all: [],
      selected: localStorage.getItem('adsmanager-adsets-selected') ?
        JSON.parse(localStorage.getItem('adsmanager-adsets-selected')) : [],
      filtered: [],
      forDialogMainInfo: {},
      count: 0,
      itemsCount: 0,
      page: 1
    },
    emptyFilters: {
      search: '',
      statuses: [],
      accountsStatuses: [],
      cabsStatuses: [],
      tags: [],
      onlySelected: false,
      withSpend: false
    },
    filters: {},
    dialogs: {
      changeBudget: false,
      pause: false,
      start: false,
      duplicate: false,
      filters: false,
      multipleAssignTags: false,
      mainInfo: false
    },
    loading: {
      mainTable: false,
      multipleAssignTags: false
    },
    stat: [],
    forReviewFeedback: null,
    sorts: {},
  },
  getters: {
    ...mixinDialogGetters,

    adsets: state => state.adsets,
    filters: state => state.filters,
    selected: state => state.adsets.selected,
    count: state => state.adsets.count,
    itemsCount: state => state.adsets.itemsCount,
    page: state => state.adsets.page,
    loading: state => state.loading,
    stat: state => state.stat,
    forReviewFeedback: state => state.forReviewFeedback,
  },
  mutations: {
    ...mixinDialogMutations,
    ...mixinSetLoading,

    FILTER: state => {
      state.adsets.filtered = state.adsets.all;
    },

    SET_SPECIFIC_FILTER: (state, data) => {
      state.filters[data.filter] = data.data;
      localStorage.setItem('adsmanager-adsets-filters', JSON.stringify(state.filters));
    },

    LOAD_FILTERS: (state) => {
      if (localStorage.getItem('adsmanager-adsets-filters')) {
        state.filters = JSON.parse(localStorage.getItem('adsmanager-adsets-filters'));
      } else {
        state.filters = {...state.emptyFilters};
      }
    },

    SORT: (state, data) => {
      state.sorts = {};
      if (data.key !== 'none') {
        state.sorts['key'] = data.key;
        state.sorts['value'] = data.value;
      }
    },

    COUNT: (state, data) => {
      state.adsets.count = data;
    },

    ITEMS_COUNT: (state, data) => {
      state.adsets.itemsCount = data;
    },

    SET_PAGE: (state, data) => {
      state.adsets.page = data;
      localStorage.setItem('adsmanager-items-page', data);
    },

    FILTER_ADSETS: (state, payload) => {
      let adsets = state.adsets.all;
      const filters = state.filters;
      const accountsFilters = localStorage.getItem('adsmanager-accounts-filters') ? JSON.parse(localStorage.getItem('adsmanager-accounts-filters')) : payload.rootState.accounts.filters;
      const cabsFilters = localStorage.getItem('adsmanager-cabs-filters') ? JSON.parse(localStorage.getItem('adsmanager-cabs-filters')) : payload.rootState.cabs.filters;
      const $_ = payload.$_;

      if ($_.isEmpty(accountsFilters) || !accountsFilters.showArchived) {
        adsets = adsets.filter(adset => adset.account.archived === 0);
      }
      if ($_.isEmpty(cabsFilters) || !cabsFilters.showArchived) {
        adsets = adsets.filter(adset => adset.cab.archived === 0);
      }

      if (filters.statuses && filters.statuses.length > 0) {
        adsets = adsets.filter(adset => {
          return filters.statuses.includes(adset.effective_status);
        });
      }

      if (filters.search) {
        if (filters.search.toString().length > 0) {
          adsets = adsets.filter(adset => {
            if (adset.adset_id.toString().toLowerCase().indexOf(filters.search.toString().toLowerCase()) !== -1) {
              return true;
            }
            if (adset.name.toString().toLowerCase().indexOf(filters.search.toString().toLowerCase()) !== -1) {
              return true;
            }
            return false;
          });
        }
      }

      if (filters.accountsStatuses && filters.accountsStatuses.length > 0) {
        adsets = adsets.filter(adset => {
          return filters.accountsStatuses.includes(adset.account.status);
        });
      }

      if (filters.cabsStatuses && filters.cabsStatuses.length > 0) {
        adsets = adsets.filter(adset => {
          return filters.cabsStatuses.includes(adset.cab.status);
        });
      }

      if (filters.tags && filters.tags.length > 0) {
        adsets = adsets.filter(adset => {
          return filters.tags.some(tag => {
            if (!Array.isArray(adset.tags)) return false;
            return adset.tags.findIndex(item => item.toString() === tag.toString()) > -1;
          });
        });
      }

      if (
        typeof filters.onlySelected !== 'undefined' &&
        filters.onlySelected
      ) {
        adsets = state.adsets.selected.map(adset => adset);
      }

      if (
        typeof filters.withSpend !== 'undefined' &&
          filters.withSpend
      ) {
        adsets = adsets.filter(adset => {
          for (let stat in state.stat) {
            if (adset.id === parseInt(stat)) {
              return state.stat[stat].spend > 0;
            }
          }
        });
      }

      state.adsets.filtered = adsets;
    },

    SET_STAT: (state, stat) => {
      state.stat = stat;
    },

    SET_SELECTED: (state, data) => {
      state.adsets.selected = data;
      localStorage.setItem('adsmanager-adsets-selected', JSON.stringify(data));
    },

    SET_ALL: (state, payload) => {
      state.adsets.all = payload;
    },

    SET_FILTERED: (state, payload) => {
      state.adsets.filtered = payload;
    },

    CLEAR_FILTERS: state => {
      state.filters = {
        ...state.emptyFilters
      };
      localStorage.removeItem('adsmanager-adsets-filters');
    },

    SET_FOR_DIALOG_MAIN_INFO: (state, payload) => {
      state.adsets.forDialogMainInfo = payload;
    },
  },
  actions: {
    ...mixinDialogActions,

    setPage({commit}, data) {
      commit('SET_PAGE', data);
    },

    async loadAdsets({state, commit, rootState}) {
      try {
        commit('SET_LOADING', {
          param: 'mainTable',
          value: true,
        });
        let url = '/fb-adsets?';
        const restResourceService = new RestResource();

        let response = await restResourceService.sendRequest(state, rootState.main.profile.columns, url);

        if (response.status === 200) {
          commit('SET_ALL', response.data.data);
          commit('FILTER', response.data.data);
          commit('COUNT', response.data.meta.last_page);
          commit('ITEMS_COUNT', response.data.meta.total);
          commit('SET_PAGE', response.data.meta.current_page);
        }
      } catch (_) {
        //Do Nothing
      } finally {
        commit('SET_LOADING', {
          param: 'mainTable',
          value: false,
        });
      }

    },

    async saveSelected(context, data) {
      context.commit('SET_SELECTED', data);
    },

    async clearSelected(context) {
      context.commit('SET_SELECTED', []);
    },

    async clear(context) {
      context.commit('SET_ALL', []);
      context.commit('SET_FILTERED', []);
      context.commit('SET_SELECTED', []);
    },

    async setSpecificFilter(context, data) {
      context.commit('SET_SPECIFIC_FILTER', data);
      context.dispatch('filterAdsets');
      context.dispatch('loadAdsets');
    },

    async filterAdsets(context) {
      // const data = {};
      // const users = context.rootState.users.users;
      // const accounts = context.rootState.accounts.accounts;
      // const cabs = context.rootState.cabs.cabs;
      // const campaigns = context.rootState.campaigns.campaigns;
      // const ads = context.rootState.ads;

      // if (users.selected.length > 0) {
      //   data.users = users.selected.map(item => item.id);
      // } else if (users.selected.length === 0 && users.filtered.length > 0) {
      //   data.users = users.filtered.map(item => item.id);
      // }
      // if (accounts.selected.length > 0) {
      //   data.accounts = accounts.selected.map(item => item.id);
      // } else if (accounts.selected.length === 0 && accounts.filtered.length > 0) {
      //   data.accounts = accounts.filtered.map(item => item.id);
      // }
      // if (cabs.selected.length > 0) {
      //   data.cabs = cabs.selected.map(item => item.cab_id);
      // } else if (cabs.selected.length === 0 && cabs.filtered.length > 0) {
      //   data.cabs = cabs.filtered.map(item => item.cab_id);
      // }
      // if (campaigns.selected.length > 0) {
      //   data.campaigns = campaigns.selected.map(item => item.campaign_id);
      // } else if (campaigns.selected.length === 0 && campaigns.filtered.length > 0) {
      //   data.campaigns = campaigns.filtered.map(item => item.campaign_id);
      // }

      // if (ads.filters.onlySelected && ads.ads.selected.length > 0) {
      //   data.ads = ads.ads.selected.map(item => item.adset_id);
      // }
      await context.dispatch('loadAdsets');
      // context.commit('FILTER_ADSETS', {
      //   rootState: context.rootState,
      //   $_: this._vm.$_
      // });
    },

    async sortAdsets(context, data) {
      context.commit('SORT', data);
      await context.dispatch('loadAdsets');
    },

    async loadFilters(context) {
      context.commit('LOAD_FILTERS');
    },
    
    async saveMultipleTags(context, data) {
      context.commit('SET_LOADING', {
        param: 'multipleAssignTags',
        value: true
      });

      const response = await this._vm.api.post('/adsets/update_multiple_tags', data).catch((e) => {
        context.dispatch('main/apiError', e, {
          root: true
        });
      });

      context.commit('SET_LOADING', {
        param: 'multipleAssignTags',
        value: false
      });

      if (response.data.success) {
        context.dispatch('main/alert', {
          color: 'success',
          message: i18n.t('common.saved')
        }, {
          root: true
        });

        context.dispatch('loadAdsets');
        return true;
      } else {
        context.dispatch('main/apiError', response.data, {
          root: true
        });
      }

      return response.data.success;
    },

    async clearFilters(context) {
      await context.commit('CLEAR_FILTERS');
      context.dispatch('filterAdsets');
      context.dispatch('loadAdsets');
    },

    async setForDialogMainInfo(context, data) {
      await context.commit('SET_FOR_DIALOG_MAIN_INFO', data);
    },

    async changeCurrency(context) {
      await context.dispatch('loadAdsets');
    },
  }
};
