import RestResource from '@/services/RestResource';

import {
  mixinDialogMutations,
  mixinDialogActions,
  mixinDialogGetters
} from '../../mixins/vuex_dialogs';
import {
  mixinSetLoading
} from '../../mixins/vuex_loading';

export default {
  namespaced: true,

  state: {
    proxy: {
      all: [],
      filtered: [],
      selected: [],
      forPermissions: {},
      forEdit: {},
      count: 0,
      itemsCount: 0,
      page: 1
    },
    filters: {
      name: '',
    },
    dialogs: {
      share: false,
      check: false,
      delete: false,
      add: false,
      permissions: false,
      edit: false,
      expirityDate: false,
      log: false
    },
    loading: {
      mainTable: false,
      share: false,
      add: false
    },
    ipChangeLog: {},
    sorts: [],
  },

  getters: {
    ...mixinDialogGetters,

    proxy: state => state.proxy,
    filters: state => state.filters,
    loading: state => state.loading,
    selected: state => state.proxy.selected,
    forPermissions: state => state.proxy.forPermissions,
    forEdit: state => state.proxy.forEdit,
    ipChangeLog: state => state.ipChangeLog,
    count: state => state.proxy.count,
    itemsCount: state => state.proxy.itemsCount,
    page: state => state.proxy.page,
  },
  mutations: {
    ...mixinDialogMutations,

    ...mixinSetLoading,

    SET_PROXY: (state, proxy) => {
      state.proxy.all = proxy;
    },

    FILTER_PROXY: state => {
      state.proxy.filtered = state.proxy.all;
    },

    SET_SELECTED: (state, payload) => {
      state.proxy.selected = payload;
    },

    SET_FILTER: (state, payload) => {
      state.filters[payload.filter] = payload.data;
    },

    SET_FOR_PERMISSIONS: (state, payload) => {
      state.proxy.forPermissions = payload;
    },

    SET_FOR_EDIT: (state, payload) => {
      state.proxy.forEdit = payload;
    },

    GET_IP_CHANGE_LOG: (state, payload) => {
      state.ipChangeLog = payload;
    },

    SET_SPECIFIC_FILTER: (state, data) => {
      state.filters[data.filter] = data.data;
      localStorage.setItem('proxy-filters', JSON.stringify(state.filters));
    },

    SORT: (state, data) => {
      state.sorts = [];
      if (data.key !== 'none') {
        state.sorts['key'] = data.key;
        state.sorts['value'] = data.value;
      }
    },

    COUNT: (state, data) => {
      state.proxy.count = data;
    },

    ITEMS_COUNT: (state, data) => {
      state.proxy.itemsCount = data;
    },

    SET_PAGE: (state, data) => {
      state.proxy.page = data;
      localStorage.setItem('proxies-items-page', data);
    },
  },
  actions: {
    ...mixinDialogActions,

    async loadProxy({state, commit}) {
      try {
        commit('SET_LOADING', {
          param: 'mainTable',
          value: true,
        });

        let url = '/proxies?';
        const restResourceService = new RestResource();

        let response = await restResourceService.getProxies(state, url);

        if (response.status === 200) {
          commit('SET_PROXY', response.data.data);
          commit('FILTER_PROXY', response.data.data);
          commit('COUNT', response.data.meta.last_page);
          commit('ITEMS_COUNT', response.data.meta.total);
          commit('SET_PAGE', response.data.meta.current_page);
        }
      } catch (_) {
        //Do Noothing
      } finally {
        commit('SET_LOADING', {
          param: 'mainTable',
          value: false,
        });
      }
    },

    async saveSelected({commit}, payload) {
      commit('SET_SELECTED', payload);
    },

    async savePermissions({commit, dispatch}, data) {
      commit('SET_LOADING', {
        param: 'share',
        value: true
      });

      let path;
      if (data.mode == 1)
        path = 'add-permissions';
      else
        path = 'remove-permissions';

      data.permissions = data.permissions.map(permission => {
        return {
          to_user_id: permission,
          type: 'admin',
        };
      });

      await this._vm.cloudApi({
        url: '/proxy/' + path,
        method: data.mode === 1 ? 'put' : 'delete',
        data: data,
      }).catch((e) => {
        dispatch(
          'main/apiError',
          {
            method: '/proxy/update_permissions',
            request: data,
            response: e,
          },
          {
            root: true
          }
        );
      });

      commit('SET_LOADING', {
        param: 'share',
        value: false
      });

      dispatch('loadProxy');
      return true;
    },

    async setFilter({commit}, payload) {
      commit('SET_FILTER', payload);
    },

    async filterProxy({commit}) {
      commit('FILTER_PROXY');
    },

    async addProxy({
      commit,
      dispatch
    }, payload) {

      commit('SET_LOADING', {
        param: 'add',
        value: true
      });

      await this._vm.cloudApi
        .post('proxies', {proxies: payload})
        .catch((e) => {
          dispatch(
            'main/apiError', {
              method: '/proxy/import',
              request: payload,
              response: e.response.data,
            }, {
              root: true
            }
          );
        });

      commit('SET_LOADING', {
        param: 'add',
        value: false
      });

      return true;
    },

    async setForPermissions({commit}, payload) {
      commit('SET_FOR_PERMISSIONS', payload);
    },

    async setForEdit({commit}, payload) {
      commit('SET_FOR_EDIT', payload);
    },

    async saveProxy({dispatch}, proxy) {
      await this._vm.cloudApi.put('proxy/' + proxy.id, proxy).catch((e) => {
        dispatch(
          'main/apiError', {
            method: 'proxy',
            request: proxy,
            response: e.response.data,
          }, {
            root: true
          }
        );
      });

      dispatch('loadProxy');
    },

    async getIpChangeLog(context, data) {
      const response = await this._vm.api.post('/proxy/change_ip_log', {proxy_id: data.id});
      if (response.data.success) {
        let payload = {
          id: data.id,
          name: data.name,
          log: response.data.data
        };
        context.commit('GET_IP_CHANGE_LOG', payload);
      }
    },

    async clearSelected(context) {
      context.commit('SET_SELECTED', []);
    },

    async setSpecificFilter(context, data) {
      context.commit('SET_SPECIFIC_FILTER', data);
      await context.dispatch('loadProxy');
    },

    async sortProxies(context, data) {
      context.commit('SORT', data);
      await context.dispatch('loadProxy');
    },

    setPage({commit}, data) {
      commit('SET_PAGE', data);
    },
  }
};
