export default {
  namespaced: true,
  state: {
    page: 1,
    itemsPerPage: 10,
    pageCount: 1,
    total: 0,
    from: 1,
    to: 10,
    sortBy: [],
    sortDesc: false,
    filter: {
      key: '',
      value: ''
    },
    filtersSolo: [],
    filters: [],
  },
  mutations: {
    SET_TOTAL: (state, total) => state.total = total,
    SET_ITEMS_PER_PAGE: (state, perPage) => state.itemsPerPage = perPage,
    SET_PAGE: (state, page) => state.page = page,
    SET_SORT_BY: (state, sortBy) => state.sortBy = sortBy,
    SET_SORT_DESC: (state, sortDesc) => state.sortDesc = sortDesc,
    SET_FILTER: (state, filter) => state.filter = filter,
    SET_PAGE_COUNT: (state, count) => state.pageCount = count,
    SET_FROM: (state, from) => state.from = from,
    SET_TO: (state, to) => state.to = to,
    SET_FILTERS: (state, filters) => {
      console.log('setFilters');
      if (state.filters.length > 0) {
        let index = state.filters.findIndex(filter => filter.type === filters.type);
        if (index > -1)
          state.filters.splice(index, 1, filters);
        else
          state.filters.push(filters);
      } else
        state.filters.push(filters);
    },
    SET_FILTERS_SOLO: (state, soloFilter) => {
      if (state.filtersSolo.length > 0) {
        let number = state.filtersSolo.findIndex(filter => filter.type === soloFilter.type);
        if (number > -1)
          state.filtersSolo.splice(number, 1, soloFilter);
        else
          state.filtersSolo.push(soloFilter);
      } else
        state.filtersSolo.push(soloFilter);
    },
    RESET_ALL_FILTERS: (state) => {
      state.filtersSolo = [];
      state.filters = [];
    }
  },
  actions: {}
};
