import { getField, updateField }      from 'vuex-map-fields';

import i18n                           from '../../i18n';
import {
  mixinDialogMutations,
  mixinDialogActions,
  mixinDialogGetters
} from '../../mixins/vuex_dialogs';

export default {
  namespaced: true,
  state: {
    jobs: {
      all: []
    },
    selected: [],
    period: 'today',
    loading: {
      restartJob: false,
      deleteJob: false
    },
    dialogs: {
      mainTableLogDrawer: false
    },
    jobForLog: null
  },
  getters: {
    ...mixinDialogGetters,
    getField,

    jobs: state => state.jobs,
    selected: state => state.selected,
    period: state => state.period,
    loading: state => state.loading,
    jobForLog: state => state.jobForLog,
  },
  mutations: {
    ...mixinDialogMutations,
    updateField,

    SET_JOBS: (state, payload) => {
      state.jobs.all = payload;
    },

    SET_PERIOD: (state, payload) => {
      state.period = payload;
    },

    SET_SELECTED_JOBS: (state, payload) => {
      state.selected = payload;
    },

    UPDATE_STATUS: (state, payload) => {
      let jobs = state.jobs.all;
      for (let jobIndex = 0; jobIndex < jobs.length; jobIndex++) {
        if (jobs[jobIndex].id === payload.id) {
          jobs[jobIndex].error = payload.data.error,
          jobs[jobIndex].has_error = payload.data.has_error,
          jobs[jobIndex].status = payload.data.status;
        }
      }
    },

    SET_LOADING: (state, data) => {
      state.loading[data.param] = data.value;
    },

    SET_JOB_FOR_LOG: (state, payload) => {
      state.jobForLog = payload;
    },

    UPDATE_STATUSES: (state, payload) => {
      // console.log(payload);
      for (const jobStatus in payload) {
        const job = state.jobs.all.find(i => parseInt(jobStatus, 10) === parseInt(i.id, 10));
        console.log(job);
        if (job) {
          job.status = payload[jobStatus].status,
          job.has_error = payload[jobStatus].has_error,
          job.error = payload[jobStatus].error;
        }
      }
    }
  },
  
  actions: {
    ...mixinDialogActions,
    
    async setJobs(context, data) {
      const response = await this._vm.api(`/jobs/?period=${data}`);
      if (response && typeof response.data.data !== 'undefined') {
        context.commit('SET_JOBS', response.data.data);
      }
    },

    setPeriod(context, data) {
      context.commit('SET_PERIOD', data);
    },

    setSelectedJobs({commit}, payload) {
      commit('SET_SELECTED_JOBS', payload);
    },

    clearSelected(context) {
      context.commit('SET_SELECTED_JOBS', []);
    },

    async updateStatus(context, data) {
      const response = await this._vm.api.get(`/jobs/status/${data}`);
      if (response && typeof response.data.data !== 'undefined') {
        let payload = {
          id: data,
          data: response.data.data
        };
        context.commit('UPDATE_STATUS', payload);
      }
    },

    async updateStatuses(context, data) {
      const response = await this._vm.api.get(`/jobs/statuses/?ids=${data.join(',')}`);
      if (response.status === 200 && response.data && response.data.data) {
        context.commit('UPDATE_STATUSES', response.data.data);
      }
    },

    async deleteJob({commit, dispatch, getters}, data) {
      commit('SET_LOADING', {
        param: 'deleteJob',
        value: true
      });

      const response = await this._vm.api.get(`/jobs/delete_bulk?ids=${data}`).catch((e) => {
        dispatch('main/apiError', e, {
          root: true
        });
      });
      commit('SET_LOADING', {
        param: 'deleteJob',
        value: false
      });

      if (response.data.success) {
        dispatch('main/alert', {
          color: 'success',
          message: i18n.t('common.deleted')
        }, {
          root: true
        });
        dispatch('setJobs', getters.period);
        dispatch('clearSelected');
      } else {
        dispatch('main/apiError', response.data, {
          root: true
        });
      }

      return response.data.success;
    },

    async restartJob({commit, dispatch, getters}, data) {
      commit('SET_LOADING', {
        param: 'restartJob',
        value: true
      });

      const response = await this._vm.api.get(`/jobs/reset_bulk?ids=${data}`).catch((e) => {
        dispatch('main/apiError', e, {
          root: true
        });
      });
      commit('SET_LOADING', {
        param: 'restartJob',
        value: false
      });

      if (response.data.success) {
        dispatch('main/alert', {
          color: 'success',
          message: i18n.t('common.restarted')
        }, {
          root: true
        });
        let ids = data.split(',');
        for (let i = 0; i < ids.length; i++) {
          let payload = {
            id: parseInt(ids[i]),
            data: {
              error: false,
              has_error: 0,
              status: 0
            }
          };
          commit('UPDATE_STATUS', payload);
        } 
        dispatch('setJobs', getters.period);
        dispatch('clearSelected');
      } else {
        dispatch('main/apiError', response.data, {
          root: true
        });
      }

      return response.data.success;
    },

    async setJobForLog({commit}, payload) {
      const response = await this._vm.api(`/jobs/log/${payload}`);
      if (response.data.success) {
        let data = {
          id: payload,
          log: response.data.data
        };
        commit('SET_JOB_FOR_LOG', data);
      }
    },
    
  }
};