import emptyBundle from '@/constants/tiktok/upload/empty_bundle';
import {
  mixinMainTableMutations,
  mixinMainTableActions,
  mixinMainTableGetters,
  mixinMainTableState } from '@/mixins/tiktok/common/vuex_main_table';
import {
  mixinDialogMutations,
  mixinDialogActions,
  mixinDialogGetters } from '@/mixins/vuex_dialogs';
import { getField, updateField } from 'vuex-map-fields';

export default {
  namespaced: true,
  state: {
    ...mixinMainTableState,
    bundle: null,
    $validation: null,
    dialogs: {
      mainDialog: false,
      campaignNameMacros: false,
      aboutBudget: false,
      budgetRandomize: false,
      adsetNameMacros: false,
      adNameMacros: false,
      availableSpintax: false,
      bulkDelete: false,
    },
    selected: []
  },

  getters: {
    ...mixinDialogGetters,
    ...mixinMainTableGetters,
    getField,

    bundle: state => state.bundle,
    $validation: state => state.$validation,
    selected: state => state.selected,
  },
  mutations: {
    ...mixinDialogMutations,
    ...mixinMainTableMutations,
    updateField,

    SET_BUNDLE: (state, payload) => {
      state.bundle = payload;
    },

    SET_BUNDLE_PROPERTY: (state, payload) => {
      state.bundle[payload.key] = payload.value;
    },

    SET_VALIDATION: (state, payload) => {
      state.$validation = payload;
    },

    CLEAR_SELECTED: (state) => {
      state.selected = [];
    },
  },
  actions: {
    ...mixinDialogActions,
    ...mixinMainTableActions,

    async setBundle({ commit }, data) {
      const bundle = this._vm.$_.cloneDeep(emptyBundle);

      if (data) {
        commit('SET_BUNDLE', Object.assign(bundle, data));
        return;
        // bundle.name = data.name && typeof data.name === 'string' ? data.name : bundle.name;
        // bundle.objective = data.objective && typeof data.objective === 'string' ? data.objective : bundle.objective;
      }

      commit('SET_BUNDLE', bundle);
    },

    async clearBundle({ commit }) {
      commit('SET_BUNDLE', null);
    },

    async setBundleProperty({ commit }, data) {
      commit('SET_BUNDLE_PROPERTY', data);
    },

    async setValidation({ commit }, data) {
      commit('SET_VALIDATION', data);
    },

    async clearSelected({ commit }) {
      commit('CLEAR_SELECTED');
    },
  }
};
