<script>
import InfoBar                                 from '@/components/InfoBar';
import { Integrations }                        from '@sentry/tracing';
import * as Sentry                             from '@sentry/vue';
import moment                                  from 'moment';
import {mapGetters}                            from 'vuex';
import Vue                                     from 'vue';

import Alert                                   from './components/Alert';
import ApiError                                from './components/ApiError';
import Sidebar                                 from './components/Sidebar';
import FbApiLog                                from './components/fb-api-log/fbApiLog';
import SidebarDialogFeedback                   from './components/sidebar/SidebarDialogFeedback';
import Topbar                                  from './components/topbar/Topbar';

export default {
  name: 'App',

  components: {
    Alert,
    InfoBar,
    FbApiLog,
    Topbar,
    Sidebar,
    SidebarDialogFeedback,
    ApiError
  },
  data() {
    return {
      moment
    };
  },

  computed: {
    ...mapGetters({
      dialogs: 'main/dialogs',
      fbApiLogDialogs: 'fbApiLog/dialogs',
      profile: 'main/profile',
      sidebarStatus: 'main/sidebar',
      filters: 'adsmanager/filters',
    }),

    showAppInner() {
      if (['AuthLogin', 'AuthRegistration'].indexOf(this.$route.name) === -1) {
        return true;
      }
      return false;
    }
  },

  async beforeMount() {
    // await this.$store.dispatch('settings/loadSettings');
  },

  async created() {
    this.setInnerHeight();
    this.setInterceptors();
    // await this.$store.dispatch('main/loadProfile');

    if (!['localhost', 'surge.sh'].includes(location.hostname) && this.profile.send_debug_data) {
      this.initSentry();
    }
    
    await this.$store.dispatch('adsmanager/changeCurrency', this.profile.currency);
    // this.$store.dispatch('news/loadUnreadNewsCount', this.profile.last_news);

    // setInterval(() => {
    //   this.$store.dispatch('news/loadUnreadNewsCount', this.profile.last_news);
    // }, 1800000);
    // this.$vuetify.theme.dark = this.profile.theme_mode === 'dark' ? true : false;

    // if (!localStorage.getItem('language')) {
    //   localStorage.setItem('language', this.profile.language || 'en');
    //   location.reload();
    // }

    setInterval(async () => {
      if (
        moment().format('H:mm') === '0:00' &&
        this.filters.dates.startDate === moment().subtract(1, 'days').startOf('day').format('YYYY-MM-DD') &&
        this.filters.dates.endDate === moment().subtract(1, 'days').startOf('day').format('YYYY-MM-DD')
      ) {
        await this.$store.dispatch('adsmanager/setFiltersDates', {
          startDate: this.moment().format('YYYY-MM-DD'),
          endDate: this.moment().format('YYYY-MM-DD')
        });
        location.reload();
      }
      // await this.$store.dispatch('main/loadProfile');
      await this.$store.dispatch('adsmanager/changeCurrency', this.profile.currency);
    }, 60000);
  },

  methods: {
    setInnerHeight() {
      this.$store.dispatch('main/setInnerHeight', window.innerHeight);
      window.addEventListener('resize', (data) => {
        this.$store.dispatch('main/setInnerHeight', data.target.innerHeight);
      });
    },
    
    initSentry() {
      Sentry.init({
        Vue,
        dsn: 'https://93e2bc09dba04a8c928b9d02af6f2001@o1037856.ingest.sentry.io/6006936',
        integrations: [new Integrations.BrowserTracing()],
        tracesSampleRate: 0,
        release: 'SENTRY_RELEASE',
        ignoreErrors: [
          'createError(axios/lib/core/createError)',
          'Network Error',
          'Request aborted'
        ]
      });
    },

    setInterceptors () {
      // this.api.interceptors.response.use(response => {
      //   if (response.data && response.data.response && typeof response.data.response === 'string') {
      //     response.data.response = decodeURIComponent(response.data.response);
      //   }

      //   return response;
      // }, error => {
      //   return Promise.reject(error);
      // });
      this.api.interceptors.response.use(
        (response) => response,
        (error) => {
          if (['AuthLogin', 'AuthRegistration'].indexOf(this.$route.name) === -1) {
            this.$store.dispatch('main/apiError', error);
          }
          return Promise.reject(error);
        }
      );
    }
  }
};
</script>
