import {
  mixinDialogMutations,
  mixinDialogActions,
  mixinDialogGetters
} from '@/mixins/vuex_dialogs';
import {
  mixinSetLoading
} from '@/mixins/vuex_loading';
import RestResource              from '@/services/RestResource';
import { getField, updateField } from 'vuex-map-fields';

export default {
  namespaced: true,
  state: {
    allBms: [],
    bms: {
      all: [],
      selected: localStorage.getItem('adsmanager-bms-selected') ?
        JSON.parse(localStorage.getItem('adsmanager-bms-selected') || '') :
        [],
      filtered: [],
      forInviteLinksCreation: [],
      forDialogMainInfo: {},
      count: 0,
      itemsCount: 0,
      page: 1
    },
    loading: {
      mainTable: false,
      createCabs: false
    },
    dialogs: {
      createCabs: false,
      createInviteLinks: false,
      domainManagement: false,
      howToGetBmToken: false,
      bmCenter: false,
      changePrimaryPage: false,
      verifyEmail: false,
    },
    stat: [],
    emptyFilters: {
      search: ''
    },
    filters: {},
    bmsForCreateCabsDialog: [],
    newCabsOptions: {},
    createInviteLinks: [],
    bmForDialog: {},
    sorts: {},
  },
  getters: {
    ...mixinDialogGetters,
    getField,

    bms: (state) => state.bms,
    filters: (state) => state.filters,
    selected: (state) => state.bms.selected,
    count: state => state.bms.count,
    itemsCount: state => state.bms.itemsCount,
    page: state => state.bms.page,
    loading: (state) => state.loading,
    stat: (state) => state.stat,
    columns: state => state.profile.columns,
    bmsForCreateCabsDialog: (state) => state.bmsForCreateCabsDialog,
    newCabsOptions: (state) => state.newCabsOptions,
    createInviteLinks: (state) => state.createInviteLinks,
    bmForDialog: (state) => state.bmForDialog,
    allBms: (state) => state.allBms,

  },
  mutations: {
    ...mixinDialogMutations,
    ...mixinSetLoading,
    updateField,

    FILTER: (state) => {
      state.bms.filtered = state.bms.all;
    },

    SORT: (state, data) => {
      state.sorts = {};
      if (data.key !== 'none') {
        state.sorts['key'] = data.key;
        state.sorts['value'] = data.value;
      }
    },

    COUNT: (state, data) => {
      state.bms.count = data;
    },

    ITEMS_COUNT: (state, data) => {
      state.bms.itemsCount = data;
    },

    SET_PAGE: (state, data) => {
      state.bms.page = data;
      localStorage.setItem('adsmanager-items-page', data);
    },

    SET_SPECIFIC_FILTER: (state, data) => {
      state.filters[data.filter] = data.data;
      localStorage.setItem('adsmanager-bms-filters', JSON.stringify(state.filters));
    },

    LOAD_FILTERS: (state) => {
      if (localStorage.getItem('adsmanager-bms-filters')) {
        state.filters = JSON.parse(localStorage.getItem('adsmanager-bms-filters'));
      } else {
        state.filters = {...state.emptyFilters};
      }
    },

    SET_STAT: (state, stat) => {
      state.stat = stat;
    },

    SET_SELECTED: (state, data) => {
      state.bms.selected = data;
      localStorage.setItem('adsmanager-bms-selected', JSON.stringify(data));
    },

    SET_ALL: (state, bms) => {
      state.bms.all = bms;
    },

    SET_FILTERED: (state, bms) => {
      state.bms.filtered = bms;
    },

    SET_ALL_EXISTED_BMS: (state, bms) => {
      state.allBms = bms;
    },

    FILTER_BMS: (state, payload) => {
      let bms = state.bms.all;
      const filters = state.filters;
      const accountsFilters = localStorage.getItem('adsmanager-accounts-filters') ? JSON.parse(localStorage.getItem('adsmanager-accounts-filters')) : payload.rootState.accounts.filters;
      const $_ = payload.$_;

      if ($_.isEmpty(accountsFilters) || !accountsFilters.showArchived) {
        bms = bms.filter(bm => bm.account.archived === 0);
      }

      if (filters.search) {
        if (filters.search.toString().length > 0) {
          bms = bms.filter(bm => {
            if (bm.business_id.toString().toLowerCase().indexOf(filters.search.toString().toLowerCase()) !== -1) {
              return true;
            }
            if (bm.name.toString().toLowerCase().indexOf(filters.search.toString().toLowerCase()) !== -1) {
              return true;
            }
            return false;
          });
        }
      }

      // if (payload && payload.users) {
      //   bms = bms.filter(bm => payload.users.includes(bm.user_id));
      // }
      // if (payload && payload.accounts) {
      //   bms = bms.filter(bm => payload.accounts.includes(bm.account.id));
      // }
    
      state.bms.filtered = bms;
    },

    SET_BMS_FOR_CREATE_CABS_DIALOG: (state, payload) => {
      state.bmsForCreateCabsDialog = payload;
    },

    SET_NEW_CABS_OPTIONS: (state, payload) => {
      state.newCabsOptions = {...payload};
    },

    SET_CREATING_CAB_RESPONSE: (state, payload) => {
      state.newCabsOptions[payload.business_id][payload.cab].response = payload.response;
    },

    SET_FOR_INVITE_LINKS_CREATION: (state, data) => {
      state.bms.forInviteLinksCreation.push(data);
    },

    SET_BM_FOR_DIALOG: (state, payload) => {
      state.bmForDialog = payload;
    }
    
  },
  actions: {
    ...mixinDialogActions,

    setPage({commit}, data) {
      commit('SET_PAGE', data);
    },

    async loadBms({state, commit, rootState}) {
      try {
        commit('SET_LOADING', {
          param: 'mainTable',
          value: true,
        });
        let url = '/fb-businesses?';

        const restResourceService = new RestResource();

        let response = await restResourceService.sendRequest(state, rootState.main.profile.columns, url);

        if (response.status === 200) {
          commit('SET_ALL', response.data.data);
          commit('FILTER', response.data.data);
          commit('COUNT', response.data.meta.last_page);
          commit('ITEMS_COUNT', response.data.meta.total);
          commit('SET_PAGE', response.data.meta.current_page);
        }
      } catch (_) {
        //Do Nothing
      } finally {
        commit('SET_LOADING', {
          param: 'mainTable',
          value: false,
        });
      }

    },

    async loadAllBms(context) {
      const data = {
        users_ids: -1,
        accounts_ids: -1,
      };

      const response = await this._vm.api.post('/businesses/simple_list', data);
      if (response.status === 200) {
        console.log(response);
        context.commit('SET_ALL_EXISTED_BMS', response.data.data);
      }

      return true;
    },

    async saveSelected(context, data) {
      context.commit('SET_SELECTED', data);
    },

    async clearSelected({
      commit
    }) {
      commit('SET_SELECTED', []);
    },

    async addBusiness({
      commit,
      dispatch
    }, payload) {
      commit('SET_LOADING', {
        param: 'addDialog',
        value: true,
      });

      const response = await this._vm.api
        .post('/bms/add', payload)
        .catch((e) => {
          dispatch('main/apiError', e, {
            root: true,
          });
        });

      commit('SET_LOADING', {
        param: 'addDialog',
        value: false,
      });

      if (response.data.success || false) {
        dispatch('loadBusinesses');
      } else {
        dispatch(
          'main/apiError', {
            request: payload,
            response: response.data,
          }, {
            root: true,
          }
        );
      }

      return response.data.success || false;
    },

    async clear(context) {
      context.commit('SET_ALL', []);
      context.commit('SET_FILTERED', []);
      context.commit('SET_SELECTED', []);
    },

    async setSpecificFilter(context, data) {
      await context.commit('SET_SPECIFIC_FILTER', data);
      // context.dispatch('filterBms');
      await context.dispatch('loadBms');
    },

    async filterBms(context) {
      // const data = {};
      // const users = context.rootState.users.users;
      // const accounts = context.rootState.accounts.accounts;

      // if (users.selected.length > 0) {
      //   data.users = users.selected.map(item => item.id);
      // } else if (users.selected.length === 0 && users.filtered.length > 0) {
      //   data.users = users.filtered.map(item => item.id);
      // }

      // if (accounts.selected.length > 0) {
      //   data.accounts = accounts.selected.map(item => item.id);
      // } else if (accounts.selected.length === 0 && accounts.filtered.length > 0) {
      //   data.accounts = accounts.filtered.map(item => item.id);
      // }

      // context.commit('FILTER_BMS', {
      //   rootState: context.rootState,
      //   $_: this._vm.$_
      // });

      await context.dispatch('loadBms');

    },

    async sortBms(context, data) {
      await context.commit('SORT', data);
      await context.dispatch('loadBms');
    },

    async changeCurrency(context) {
      await context.dispatch('loadBms');
    },

    async loadFilters(context) {
      context.commit('LOAD_FILTERS');
    },

    setBmsForCreateCabsDialog(context, data) {
      context.commit('SET_BMS_FOR_CREATE_CABS_DIALOG', data);
    },

    saveNewCabsOptions({commit}, payload) {
      commit('SET_NEW_CABS_OPTIONS', payload);
    },

    async createCabs({commit, dispatch}, data) {      
      try {
        commit('SET_LOADING', {
          param: 'createCabs',
          value: true
        });
        const response = await this._vm.api.post('/fb-cabs', {cabs: data}).catch((e) => {
          dispatch('main/apiError', e, { root: true });
        });

        if (response && response.data) {
          // commit('SET_CREATING_CAB_RESPONSE', {
          //   business_id: data.data.business_id,
          //   cab: data.cab,
          //   response: response.data.response
          // });
        }
      } catch (_) {
        // Do nothing
      } finally {
        commit('SET_LOADING', {
          param: 'createCabs',
          value: false
        });
      }
    },

    refreshCab(context, data) {
      this._vm.api(`/accounts/update_cabs_list/${data}`).catch((e) => {
        context.dispatch('main/apiError', e, {
          root: true
        });
      });
    },

    setForInviteLinksCreation(context, data) {
      context.commit('SET_FOR_INVITE_LINKS_CREATION', data);
    },

    async setBmForDialog (context, bm) {
      bm.tab = 'adAccounts';
      context.commit('SET_BM_FOR_DIALOG', bm);
    }
  },
};
