<template>
  <div style="padding-left: 8px;">
    <v-switch
      v-model="$vuetify.theme.dark"
      dense
      class="mt-1"
    >
      <template #label>
        <div v-if="!mini">
          <span style="white-space: nowrap;">
            Dark mode
          </span>
        </div>
      </template>
    </v-switch>
  </div>
</template>

<script>
export default {
  name: 'SidebarDarkMode',
    
  props: {
    mini: {
      type: Boolean,
      default: true
    }
  },
  watch: {
    '$vuetify.theme.dark': {
      handler(mode) {
        // try {
        //   this.api.post('/profile/update_theme_mode', {theme_mode: mode ? 'dark' : 'light'});
        // } catch (error) {
        //   console.log(error);
        // }
        localStorage.setItem('dark-mode', mode);
      }
    }
  },
};
</script>