import { getField, updateField }      from 'vuex-map-fields';

import i18n                           from '../../i18n';
import {
  mixinDialogMutations,
  mixinDialogActions,
  mixinDialogGetters
} from '../../mixins/vuex_dialogs';

export default {
  namespaced: true,
  state: {
    groups: {
      all: [],
      filtered: [],
      selected: localStorage.getItem('autorules-groups-selected') ?
        JSON.parse(localStorage.getItem('autorules-groups-selected')) : []
    },
    emptyFilters: {
      search: ''
    },
    filters: {},
    loading: {
      deleteGroup: false,
      createRulesGroup: false,
      editRulesGroup: false,
      share: false
    },
    dialogs: {
      rulesGroupDialog: false,
      share: false
    },
    rulesGroupDialogMode: 'create',
    group: {
      name: null,
      rules: []
    },
    rules: [],
    showWarnings: false
  },

  getters: {
    ...mixinDialogGetters,
    getField,

    groups: state => state.groups,
    group: state => state.group,
    rules: state => state.rules,
    filters: state => state.filters,
    loading: state => state.loading,
    rulesGroupDialogMode: state => state.rulesGroupDialogMode,
    showWarnings: state => state.showWarnings
  },
  
  mutations: {
    ...mixinDialogMutations,
    updateField,

    SET_GROUPS: (state, payload) => {
      state.groups.all = payload;
    },

    SET_SELECTED: (state, data) => {
      state.groups.selected = data;
      localStorage.setItem('autorules-groups-selected', JSON.stringify(data));
    },

    SET_SPECIFIC_FILTER: (state, data) => {
      state.filters[data.filter] = data.data;
      localStorage.setItem('autorules-groups-filters', JSON.stringify(state.filters));
    },

    LOAD_FILTERS: (state) => {
      if (localStorage.getItem('autorules-groups-filters')) {
        state.filters = JSON.parse(localStorage.getItem('autorules-groups-filters'));
      } else {
        state.filters = {...state.emptyFilters};
      }
    },

    FILTER_GROUPS (state) {
      let groups = state.groups.all;
      const filters = state.filters;

      if (filters.search) {
        if (filters.search.toString().length > 0) {
          groups = groups.filter(group => {
            if (group.name.toString().toLowerCase().indexOf(filters.search.toString().toLowerCase()) !== -1) {
              return true;
            }
            return false;
          });
        }
      }

      state.groups.filtered = groups;
    },

    SET_LOADING: (state, data) => {
      state.loading[data.param] = data.value;
    },

    SET_RULES_GROUP_DIALOG_MODE: (state, data) => {
      state.rulesGroupDialogMode = data;
    },

    SET_RULES: (state, payload) => {
      state.rules = payload;
    },
    
    SET_WARNING: (state, payload) => {
      state.showWarnings = payload;
    },

    SET_GROUP: (state, payload) => {
      state.group = payload;
    },
  },
  
  actions: {
    ...mixinDialogActions,
    
    async loadGroups({commit}) {
      const response = await this.api.get('/fb_rules_group');

      if (response.data.data) {
        commit('SET_GROUPS', response.data.data);
      }
      commit('FILTER_GROUPS');
    },

    setSelected(context, payload) {
      context.commit('SET_SELECTED', payload);
    },

    clearSelected(context) {
      context.commit('SET_SELECTED', []);
    },

    setSpecificFilter(context, data) {
      context.commit('SET_SPECIFIC_FILTER', data);
      context.commit('FILTER_GROUPS');
    },

    loadFilters(context) {
      context.commit('LOAD_FILTERS');
    },

    async deleteGroup(context, data) {
      context.commit('SET_LOADING', {
        param: 'deleteGroup',
        value: true
      });

      const response = await this.api.delete('/fb_rules_group', { params: data }).catch((e) => {
        context.dispatch('main/apiError', e, {
          root: true
        });
      });

      context.commit('SET_LOADING', {
        param: 'deleteGroup',
        value: false
      });

      if (response.status >= 200 && response.status < 300)  {
        context.dispatch('main/alert', {
          color: 'success',
          message: i18n.t('common.deleted')
        }, {
          root: true
        });
        context.dispatch('loadGroups');
        context.dispatch('clearSelected');
      } else {
        context.dispatch('main/apiError', response.data, {
          root: true
        });
      }

      return response.data.success;
    },
    
    setRulesGroupDialogMode(context, data) {
      context.commit('SET_RULES_GROUP_DIALOG_MODE', data);
    },

    async loadRules({commit}) {
      const response = await this._vm.api.get('/fb_rules');
      if (typeof response.data.data !== 'undefined') {
        commit('SET_RULES', response.data.data);
      }
    },

    setWarning(context, data) {
      context.commit('SET_WARNING', data);
    },

    setLoading(context, data) {
      context.commit('SET_LOADING', data);
    },

    setGroup(context, data) {
      context.commit('SET_GROUP', data);
    },

    clearGroup(context) {
      let group = {
        name: null,
        rules: []
      };
      context.commit('SET_GROUP', group);
    },

    async createRulesGroup(context, data) {
      context.commit('SET_LOADING', {
        param: 'createRulesGroup',
        value: true
      });

      const response = await this.api.post('/fb_rules_group', data).catch((e) => {
        context.dispatch('main/apiError', e, {
          root: true
        });
      });

      context.commit('SET_LOADING', {
        param: 'createRulesGroup',
        value: false
      });

      if (response.status >= 200 && response.status < 300) {
        context.dispatch('main/alert', {
          color: 'success',
          message: i18n.t('common.created')
        }, {
          root: true
        });
        context.dispatch('loadGroups');
      } else {
        context.dispatch('main/apiError', response.data, {
          root: true
        });
      }

      return response.data;
    },

    async editRulesGroup(context, data) {
      context.commit('SET_LOADING', {
        param: 'editRulesGroup',
        value: true
      });
      const response = await this.api.put(
          `/fb_rules_group/${data.id}`,
          { rule_ids: data.rules, name: data.name }
      ).catch((e) => {
        context.dispatch('main/apiError', e, {
          root: true
        });
      });

      context.commit('SET_LOADING', {
        param: 'editRulesGroup',
        value: false
      });

      if (response.status >= 200 && response.status < 300) {
        context.dispatch('main/alert', {
          color: 'success',
          message: i18n.t('common.saved')
        }, {
          root: true
        });
        context.dispatch('loadGroups');
      } else {
        context.dispatch('main/apiError', response.data, {
          root: true
        });
      }

      return response.data;
    },

    async multipleDuplicate(context, data) {
      let payload = {
        group_id: data
      };
      const response = await this._vm.api.post('/fb_rules/duplicate_rules_group', payload).catch((e) => {
        context.dispatch('main/apiError', e, {
          root: true
        });
      });

      if (response.data.success) {
        context.dispatch('main/alert', {
          color: 'success',
          message: i18n.t('common.duplicated')
        }, {
          root: true
        });
      } else {
        context.dispatch('main/apiError', response.data, {
          root: true
        });
      }
      return response.data.success;
    },
  }
};