import RestResource                                                   from '@/services/RestResource';

import i18n                                                           from '../../i18n';
import {mixinDialogMutations, mixinDialogActions, mixinDialogGetters} from '../../mixins/vuex_dialogs';
import { mixinSetLoading }                                            from '../../mixins/vuex_loading';

// import i18n from '../../i18n';

export default {
  namespaced: true,
  state: {
    users: {
      all: [],
      selected: localStorage.getItem('adsmanager-users-selected')
        ? JSON.parse(localStorage.getItem('adsmanager-users-selected') || '')
        : [],
      filtered: [],
      forEdit: {},
      count: 0,
      itemsCount: 0,
      page: 1
    },
    loading: {
      mainTable: false,
      addDialog: false,
      editDialog: false,
      assignTags: false
    },
    dialogs: {
      add: false,
      edit: false,
      assignTags: false,
      filters: false
    },
    stat: [],
    emptyFilters: {
      withSpend: false,
      showDeleted: false,
      tags: [],
    },
    filters: {},
    sorts: [],
  },
  getters: {
    ...mixinDialogGetters,

    allUsers: state => state.users.all,
    users: (state) => state.users,
    filters: state => state.filters,
    selected: (state) => state.users.selected,
    loading: (state) => state.loading,
    stat: (state) => state.stat,
    forEdit: state => state.users.forEdit,
    count: state => state.users.count,
    itemsCount: state => state.users.itemsCount,
    page: state => state.users.page,
  },
  mutations: {
    ...mixinDialogMutations,
    ...mixinSetLoading,

    FILTER: state => {
      state.users.filtered = state.users.all;
    },

    SET_ALL_USERS: (state, users) => {
      state.users.all = users;
    },

    SORT: (state, data) => {
      state.sorts = [];
      if (data.key !== 'none') {
        state.sorts['key'] = data.key;
        state.sorts['value'] = data.value;
      }
    },

    COUNT: (state, data) => {
      state.users.count = data;
    },

    ITEMS_COUNT: (state, data) => {
      state.users.itemsCount = data;
    },

    SET_PAGE: (state, data) => {
      state.users.page = data;
      localStorage.setItem('adsmanager-items-page', data);
    },

    SET_SPECIFIC_FILTER: (state, data) => {
      state.filters[data.filter] = data.data;
      localStorage.setItem('adsmanager-users-filters', JSON.stringify(state.filters));
    },

    LOAD_FILTERS: (state) => {
      if (localStorage.getItem('adsmanager-users-filters')) {
        state.filters = JSON.parse(localStorage.getItem('adsmanager-users-filters'));
      } else {
        state.filters = {...state.emptyFilters};
      }
    },

    SET_STAT: (state, stat) => {
      state.stat = stat;
    },

    SET_SELECTED: (state, data) => {
      state.users.selected = data;
      localStorage.setItem('adsmanager-users-selected', JSON.stringify(data));
    },

    SET_USER_FOR_EDIT: (state, user) => {
      state.users.forEdit = user;
    },

    SET_LOADING: (state, data) => {
      state.loading[data.param] = data.value;
    },

    CLEAR_FILTERS: state => {
      state.filters = {...state.emptyFilters};
      localStorage.removeItem('adsmanager-users-filters');
    },
  },
  actions: {
    ...mixinDialogActions,

    setPage({commit}, data) {
      commit('SET_PAGE', data);
    },

    async loadUsers({commit, state, rootState}) {
      try {
        commit('SET_LOADING', {
          param: 'mainTable',
          value: true,
        });
        let url = '/users?';
        const restResourceService = new RestResource();

        let response = await restResourceService.sendRequest(state, rootState.main.profile.columns, url);

        commit('SET_ALL_USERS', response.data.data);
        commit('FILTER', response.data.data);
        commit('COUNT', response.data.meta.last_page);
        commit('ITEMS_COUNT', response.data.meta.total);
        commit('SET_PAGE', response.data.meta.current_page);

        return true;
      } catch (_) {
        //Do Nothing
      } finally {
        commit('SET_LOADING', {
          param: 'mainTable',
          value: false,
        });
      }

    },

    async saveSelected(context, data) {
      context.commit('SET_SELECTED', data);
    },

    async clearSelected({commit}) {
      commit('SET_SELECTED', []);
    },

    async addUser({commit, dispatch}, user) {
      commit('SET_LOADING', {
        param: 'addDialog',
        value: true,
      });

      await this._vm.cloudApi
        .post('users', user)
        .catch((e) => {
          dispatch('main/apiError', e, {
            root: true
          });
        });

      commit('SET_LOADING', {
        param: 'addDialog',
        value: false,
      });

      dispatch('loadUsers');
      dispatch('loadStat');

      return true;
    },

    async saveUser({
      commit,
      dispatch
    }, user) {
      commit('SET_LOADING', {
        param: 'addDialog',
        value: true,
      });
      console.log(user);

      await this._vm.cloudApi
        .put('users/' + user.id, user)
        .catch(error => {
          dispatch(
            'main/apiError', {
              request: user,
              response: error.response.data
            }, {
              root: true,
            }
          );
        });

      dispatch('loadUsers');
      dispatch('loadStat');

      commit('SET_LOADING', {
        param: 'addDialog',
        value: false,
      });

      return true;
    },

    async setUserForEdit({commit}, user) {
      commit('SET_USER_FOR_EDIT', user);
    },

    async deleteBulk({
      commit,
      dispatch
    }, ids) {

      await this._vm.cloudApi
        .delete('users/delete-bulk', {
          data: {ids: ids}
        })
        .catch(error => {
          dispatch(
            'main/apiError', {
              request: ids,
              response: error
            }, {
              root: true,
            }
          );
        });

      commit('SET_SELECTED', []);
      dispatch('loadUsers');
      dispatch('loadStat');

      return true;
    },
    async restoreBulk({
      commit,
      dispatch
    }, ids) {

      await this._vm.cloudApi
        .put('users/restore-bulk', {ids: ids})
        .catch(error => {
          dispatch(
            'main/apiError', {
              request: ids,
              response: error
            }, {
              root: true,
            }
          );
          return false;
        });

      commit('SET_SELECTED', []);
      dispatch('loadUsers');
      dispatch('loadStat');

      return true;
    },

    async saveMultipleTags(context, data) {
      context.commit('SET_LOADING', {
        param: 'assignTags',
        value: true
      });

      let path;
      if (data.mode === 1)
        path = 'add-tags';
      else
        path = 'remove-tags';

      await this._vm.cloudApi({
        url: '/users/' + path,
        method: data.mode === 1 ? 'put' : 'delete',
        data: data,
      }).catch((e) => {
        console.log(e);
        context.dispatch('main/apiError', e, {
          root: true
        });
      });

      context.commit('SET_LOADING', {
        param: 'assignTags',
        value: false
      });

      context.dispatch('main/alert', {
        color: 'success',
        message: i18n.t('common.saved')
      }, {
        root: true
      });

      context.dispatch('loadUsers');

      return true;
    },

    async loadFilters(context) {
      context.commit('LOAD_FILTERS');
    },

    async setSpecificFilter(context, data) {
      context.commit('SET_SPECIFIC_FILTER', data);
      await context.dispatch('loadUsers');
    },

    async sortUsers(context, data) {
      context.commit('SORT', data);
      await context.dispatch('loadUsers');
    },

    async clearFilters(context) {
      await context.commit('CLEAR_FILTERS');
      await context.dispatch('loadUsers');
    },

    async changeCurrency(context) {
      await context.dispatch('loadUsers');
    },

    async filterUsers(context) {
      await context.dispatch('loadUsers');
    },
  },
};
