<template>
  <div style="padding-left: 8px; white-space: nowrap; ">
    <flag
      :iso="languageFlag"
      style="width: 20px; background-size: 100% 100%;"
    />
    <span v-if="mini">
      {{ profile.language || 'en' }}
    </span>
    <select
      v-if="!mini"
      class="mx-3 sidebar-language-select"
      style="cursor: pointer; text-decoration: underline;"
      @change="switchLanguage($event.target.value)"
    >
      <option
        value="en"
        :selected="profile.language === 'en'"
      >
        English
      </option>
      <option
        value="ru"
        :selected="profile.language === 'ru'"
      >
        Русский
      </option>
      <option
        value="cn"
        :selected="profile.language === 'cn'"
      >
        中文
      </option>
    </select>
  </div>
</template>

<script>
import {mapGetters} from 'vuex';

export default {
  name: 'SidebarLanguageSwitcher',

  props: {
    mini: {
      type: Boolean,
      default: true,
    }
  },

  data() {
    return {
      language: '',
    };
  },

  computed: {
    ...mapGetters({
      profile: 'main/profile'
    }),

    languageFlag() {
      switch(this.profile.language) {
      case 'en':
        return 'gb';
      case 'ru':
        return 'ru';
      case 'cn':
        return 'cn';
      }

      return 'gb';
    }
  },

  methods: {
    async switchLanguage(language) {
      // try {
      //   await this.api.post('/profile/update_language', { language });
      // } catch (error) {
      //   console.log(error);
      // }
      localStorage.setItem('language', language);
      location.reload();
    }
  }
};
</script>
<style>
.theme--dark .sidebar-language-select option {
  background-color: #363636;
}
</style>