import { 
  mixinMainTableMutations,
  mixinMainTableActions,
  mixinMainTableGetters,
  mixinMainTableState } from '@/mixins/tiktok/common/vuex_main_table';
import { 
  mixinDialogMutations,
  mixinDialogActions,
  mixinDialogGetters } from '@/mixins/vuex_dialogs';
import { getField, updateField } from 'vuex-map-fields';

const userAppEmpty = {
  id: null,
  marketplaceUrl: '',
  clickTrackingUrl: '',
  impressionTrackingUrl: '',
  appIcon: null,
  appName: '',
  appId: null,
  packageName: '',
  platform: ''
};

export default {
  namespaced: true,
  state: {
    ...mixinMainTableState,

    userApp: {},
    all: [],
    selected: [],
    dialogs: {
      mainDialog: false,
      bulkDelete: false,
    },
    $validation: null,
  },
  getters: {
    ...mixinMainTableGetters,
    ...mixinDialogGetters,
    getField,

    selected: state => state.selected,
    userApp: state => state.userApp,
    $validation: state => state.$validation,
    all: state => state.all,
  },
  mutations: {
    ...mixinMainTableMutations,
    ...mixinDialogMutations,
    updateField,

    SET_USER_APP: (state, payload) => {
      state.userApp = payload;
    },

    SET_VALIDATION: (state, payload) => {
      state.$validation = payload;
    },

    SET_USER_APP_PROPERTY: (state, payload) => {
      state.userApp[payload.key] = payload.value;
    },

    SET_ALL: (state, payload) => {
      state.all = payload;
    },

    ADD_TO_ALL: (state, payload) => {
      state.all = state.all.concat(payload);
    },
  },
  actions: {
    ...mixinMainTableActions,
    ...mixinDialogActions,

    async setUserApp({ commit }, data) {
      const userApp = this._vm.$_.cloneDeep(userAppEmpty);

      if (data) {
        userApp.id = data.id ? data.id : userApp.id;
        userApp.appName = data.name ? data.name : userApp.appName;
        userApp.marketplaceUrl = data.download_url && typeof data.download_url === 'string' ?
          data.download_url : userApp.marketplaceUrl;
        userApp.clickTrackingUrl = data.click_tracking_url && typeof data.click_tracking_url === 'string' ?
          data.click_tracking_url : userApp.clickTrackingUrl;
        userApp.impressionTrackingUrl = data.impression_tracking_url && typeof data.impression_tracking_url === 'string' ?
          data.impression_tracking_url : userApp.impressionTrackingUrl;
        userApp.appIcon = data.icon ? data.icon : userApp.appIcon;
        userApp.appId = data.app_id ? data.app_id : userApp.appId;
        userApp.packageName = data.package_name ? data.package_name : userApp.packageName;
        userApp.platform = data.platform ? data.platform : userApp.platform;
      }

      commit('SET_USER_APP', userApp);
    },

    async setValidation({ commit }, data) {
      commit('SET_VALIDATION', data);
    },

    async setUserAppProperty({ commit }, data) {
      commit('SET_USER_APP_PROPERTY', data);
    },

    async loadAllUserApps({ commit, dispatch }) {
      const response = await this._vm.api('/tiktok/user-apps?page=1&perPage=100');
      const { status, data } = response;
      if (status === 200 && data && Array.isArray(data.data)) {
        commit('SET_ALL', data.data);
        if (data.meta.last_page > 1) {
          dispatch('loadNextPage', 2);
        }
      }
    },

    async loadNextPage({ commit, dispatch }, page) {
      const response = await this._vm.api(`/tiktok/user-apps?page=${page}&perPage=100`);
      const { status, data } = response;
      if (status === 200 && data && Array.isArray(data.data)) {
        commit('ADD_TO_ALL', data.data);
        if (data.meta.last_page > page) {
          dispatch('loadNextPage', page + 1);
        }
      }
    }
  }
};
