import { 
  mixinMainTableMutations,
  mixinMainTableActions,
  mixinMainTableGetters,
  mixinMainTableState } from '@/mixins/tiktok/common/vuex_main_table';
import { 
  mixinDialogMutations,
  mixinDialogActions,
  mixinDialogGetters } from '@/mixins/vuex_dialogs';
import { getField, updateField } from 'vuex-map-fields';

export default {
  namespaced: true,
  state: {
    ...mixinMainTableState,

    selected: [],
    dialogs: {
      bulkDelete: false,
      bulkDuplicate: false,
    },
    adsReasons: [],
  },
  getters: {
    ...mixinMainTableGetters,
    ...mixinDialogGetters,
    getField,

    selected: state => state.selected,
    adsReasons: state => state.adsReasons,

  },
  mutations: {
    ...mixinMainTableMutations,
    ...mixinDialogMutations,
    updateField,

    CLEAR_SELECTED: (state) => {
      state.selected = [];
    },

    SET_ADS_REASONS: (state, data) => {
      const reasons = [...state.adsReasons]
      reasons.push({
        id: data.id,
        reason: data.reason
      });

      state.adsReasons = reasons;
    }
  },
  actions: {
    ...mixinMainTableActions,
    ...mixinDialogActions,

    async clearSelected({commit}) {
      commit('CLEAR_SELECTED');
    },

    async addSelected({ rootState }, path) {
      const { selected: selectedAccounts } = rootState.tiktokAdsmanagerAccounts;
      if (Array.isArray(selectedAccounts) && selectedAccounts.length) {
        for (const account of selectedAccounts) {
          path += `accountIds[]=${account.id}&`;
        }
      }

      const { selected: selectedAdAccounts } = rootState.tiktokAdsmanagerAdAccounts;
      if (Array.isArray(selectedAdAccounts) && selectedAdAccounts.length) {
        for (const adAccount of selectedAdAccounts) {
          path += `adAccountIds[]=${adAccount.id}&`;
        }
      }

      const { selected: selectedCampaigns } = rootState.tiktokAdsmanagerCampaigns;
      if (Array.isArray(selectedCampaigns) && selectedCampaigns.length) {
        for (const campaign of selectedCampaigns) {
          path += `campaignIds[]=${campaign.id}&`;
        }
      }

      const { selected: selectedAdsets } = rootState.tiktokAdsmanagerAdsets;
      if (Array.isArray(selectedAdsets) && selectedAdsets.length) {
        for (const adset of selectedAdsets) {
          path += `adsetIds[]=${adset.id}&`;
        }
      }
      
      return path;
    },

    async sortAds({ commit, dispatch }, data) {
      commit('SORT', data);
      const requestData = {
        endpoint: 'tiktok/creatives',
        orderBy: data.key,
        orderType: data.value
      };
  
      await dispatch('loadMainTableData', requestData);
    },

    async handleAdsRejectionReason({ commit, dispatch, state}, id) {
      const response = await this.api.get(`tiktok/creatives/${id}/moderation-info`)

      if (response.data) {
        commit('SET_ADS_REASONS', {id, reason: response.data });
      }
    }
  }
};
