import currencyRates from '@/constants/tiktok/currency_rates';
import { random }    from 'lodash/number';

function randomizeBudgetFunc (budget, budgetRandomValue, type, currency) {
  if (!currency) currency = 'USD';

  const convertedBudget = convertBudgetFunc(budget, currency);
  const percent =  budgetRandomValue/100;
  
  const campaignMin = getConvertedMinBudget(50, currency);
  const adsetMin = getConvertedMinBudget(20, currency);

  const max = parseInt(convertedBudget, 10) + convertedBudget*percent;
  let min = parseInt(convertedBudget, 10) - convertedBudget*percent;
  
  if (type === 'campaign' && min < campaignMin) {
    min = campaignMin;
  }
  if (type === 'adset' && min < adsetMin) {
    min = adsetMin;
  }

  return random(max, min).toFixed(2);
}

function convertBudgetFunc (budget, currency) {
  let convertedBudget = budget;

  if (currency && currencyRates[currency]) {
    convertedBudget *= currencyRates[currency];
  }
  
  return convertedBudget.toFixed(2);
}

function getConvertedMinBudget(usdAmmount, currency) {
  return usdAmmount*currencyRates[currency].toFixed(2);
}

export {
  randomizeBudgetFunc,
  convertBudgetFunc,
  getConvertedMinBudget
};