import { 
  mixinMainTableMutations,
  mixinMainTableActions,
  mixinMainTableGetters,
  mixinMainTableState } from '@/mixins/tiktok/common/vuex_main_table';
import { 
  mixinDialogMutations,
  mixinDialogActions,
  mixinDialogGetters } from '@/mixins/vuex_dialogs';
import { getField, updateField } from 'vuex-map-fields';

export default {
  namespaced: true,
  state: {
    ...mixinMainTableState,

    selected: [],
    dialogs: {
      bulkDelete: false,
      bulkChangeBudget: false,
    },
  },
  getters: {
    ...mixinMainTableGetters,
    ...mixinDialogGetters,
    getField,

    selected: state => state.selected,
  },
  mutations: {
    ...mixinMainTableMutations,
    ...mixinDialogMutations,
    updateField,

    CLEAR_SELECTED: (state) => {
      state.selected = [];
    },
  },
  actions: {
    ...mixinMainTableActions,
    ...mixinDialogActions,
    
    async clearSelected({commit}) {
      commit('CLEAR_SELECTED');
    },

    async addSelected({ rootState }, path) {
      const { selected: selectedAccounts } = rootState.tiktokAdsmanagerAccounts;
      if (Array.isArray(selectedAccounts) && selectedAccounts.length) {
        for (const account of selectedAccounts) {
          path += `accountIds[]=${account.id}&`;
        }
      }

      const { selected: selectedAdAccounts } = rootState.tiktokAdsmanagerAdAccounts;
      if (Array.isArray(selectedAdAccounts) && selectedAdAccounts.length) {
        for (const adAccount of selectedAdAccounts) {
          path += `adAccountIds[]=${adAccount.id}&`;
        }
      }
      
      return path;
    },

    async sortCampaigns({ commit, dispatch }, data) {
      commit('SORT', data);
      const requestData = {
        endpoint: 'tiktok/campaigns',
        orderBy: data.key,
        orderType: data.value
      };
  
      await dispatch('loadMainTableData', requestData);
    },
  }
};
