export const hasFbCookies = (cookies) => {
  let cookiesArr = [];
  let hasFbCookie = false;

  try {
    cookiesArr = JSON.parse(cookies);
  } catch (e) {
    // 
  }

  if (Array.isArray(cookiesArr) && cookiesArr.length) {
    for (const cookie of cookiesArr) {
      const keys = Object.keys(cookie);
        
      for (const key of keys) {
        if (
          key.toLowerCase() === 'domain' &&
          cookie[key] &&
          cookie[key].toString().toLowerCase().includes('facebook.com')
        ) {
          hasFbCookie = true;
        }
      }
    }
  }

  return hasFbCookie;
};