import i18n                           from '../../i18n';
import {
  mixinDialogMutations,
  mixinDialogActions,
  mixinDialogGetters
} from '../../mixins/vuex_dialogs';

export default {
  namespaced: true,
  state: {
    dialogs: {
      createList: false
    },
    dialogMode: 'create',
    lists: {
      all: [],
      filtered: []
    },
    list: {
      type: '',
      name: null,
      items: null,
      shared_to_all: null,
      permissions: null
    },
    loading: {
      createList: false,
      deleteList: false,
      saveList: false

    },
    selected: [],
    emptyFilters: {
      search: '',
      geo: true,
      interests: true,
      behaviors: true
    },
    filters: {
    },
    idForEdit: null
  },
  getters: {
    ...mixinDialogGetters,

    lists: state => state.lists,
    list: state => state.list,
    loading: state => state.loading,
    selected: state => state.selected,
    filters: state => state.filters,
    dialogMode: state => state.dialogMode,
    idForEdit: state => state.idForEdit,
  },
  mutations: {
    ...mixinDialogMutations,

    SET_LISTS: (state, payload) => {
      state.lists.all = payload;
    },

    SET_LIST: (state, payload) => {
      state.list = payload;
    },

    SET_DIALOG_MODE: (state, data) => {
      state.dialogMode = data;
    },

    SET_ID_FOR_EDIT: (state, data) => {
      state.idForEdit = data;
    },

    SET_LIST_TYPE: (state, payload) => {
      state.list.type = payload;
    },

    SET_LOADING: (state, data) => {
      state.loading[data.param] = data.value;
    },

    SET_SELECTED_LISTS: (state, payload) => {
      state.selected = payload;
    },

    LOAD_FILTERS: (state) => {
      if (localStorage.getItem('upload-lists-filters')) {
        state.filters = JSON.parse(localStorage.getItem('upload-lists-filters'));
      } else {
        state.filters = {...state.emptyFilters};
      }
    },

    SET_SPECIFIC_FILTER: (state, data) => {
      state.filters[data.filter] = data.data;
      localStorage.setItem('upload-lists-filters', JSON.stringify(state.filters));
    },

    FILTER_LISTS (state) {
      let lists = state.lists.all;
      const filters = state.filters;

      if (filters.search && filters.search.toString().length > 0) {
        lists = lists.filter(list => {
          if (list.name && list.name.toString().toLowerCase().search(filters.search.toString().toLowerCase()) !== -1) {
            return true;
          }
          return false;
        });
      }

      if (
        typeof state.filters.geo !== 'undefined' &&
                !state.filters.geo
      ) {
        lists = lists.filter(list => {
          return list.type !== 'geo';
        });
      }

      if (
        typeof state.filters.interests !== 'undefined' &&
                !state.filters.interests
      ) {
        lists = lists.filter(list => {
          return list.type !== 'interests';
        });
      }

      if (
        typeof state.filters.behaviors !== 'undefined' &&
                !state.filters.behaviors
      ) {
        lists = lists.filter(list => {
          return list.type !== 'behaviors';
        });
      }
      
      state.lists.filtered = lists;
    }
  },
  
  actions: {
    ...mixinDialogActions,

    async loadLists({commit}) {
      const response = await this._vm.api('/lists');
      if (typeof response.data.data !== 'undefined') {
        commit('SET_LISTS', response.data.data);
      }
      commit('FILTER_LISTS');
    },
    
    setList(context, payload, id) {
      context.commit('SET_LIST', payload);
      if (id && id > 0) {
        context.commit('SET_ID_FOR_EDIT', id);
      }
    },

    setIdForEdit(context, payload) {
      context.commit('SET_ID_FOR_EDIT', payload);
    },

    setListType({commit}, payload) {
      commit('SET_LIST_TYPE', payload);
    },

    setDialogMode(context, data) {
      context.commit('SET_DIALOG_MODE', data);
    },

    clearList(context) {
      let list = {
        type: '',
        name: null,
        items: null,
        shared_to_all: null,
        permissions: null
      };
      context.commit('SET_LIST', list);
    },

    async createList(context, data) {
      context.commit('SET_LOADING', {
        param: 'createList',
        value: true
      });

      const response = await this._vm.api.post('/lists/create', data).catch((e) => {
        context.dispatch('main/apiError', e, {
          root: true
        });
      });

      context.commit('SET_LOADING', {
        param: 'createList',
        value: false
      });

      if (response.data.success) {
        context.dispatch('main/alert', {
          color: 'success',
          message: i18n.t('common.created')
        }, {
          root: true
        });
        context.dispatch('loadLists');
      } else {
        context.dispatch('main/apiError', response.data, {
          root: true
        });
      }

      return response.data.success;
    },

    async saveList(context, data) {
      context.commit('SET_LOADING', {
        param: 'saveList',
        value: true
      });

      const response = await this._vm.api.post('/lists/update', data).catch((e) => {
        context.dispatch('main/apiError', e, {
          root: true
        });
      });

      context.commit('SET_LOADING', {
        param: 'saveList',
        value: false
      });

      if (response.data.success) {
        context.dispatch('main/alert', {
          color: 'success',
          message: i18n.t('common.saved')
        }, {
          root: true
        });
        context.dispatch('loadLists');
      } else {
        context.dispatch('main/apiError', response.data, {
          root: true
        });
      }

      return response.data.success;
    },

    setSelectedLists({commit}, payload) {
      commit('SET_SELECTED_LISTS', payload);
    },

    async deleteList(context, data) {
      context.commit('SET_LOADING', {
        param: 'deleteList',
        value: true
      });

      const response = await this._vm.api.get(`/lists/delete?ids=${data}`).catch((e) => {
        context.dispatch('main/apiError', e, {
          root: true
        });
      });

      context.commit('SET_LOADING', {
        param: 'deleteList',
        value: false
      });

      if (response.data.success) {
        context.dispatch('main/alert', {
          color: 'success',
          message: i18n.t('common.deleted')
        }, {
          root: true
        });
        context.dispatch('loadLists');
        context.dispatch('clearSelected');
      } else {
        context.dispatch('main/apiError', response.data, {
          root: true
        });
      }

      return response.data.success;
    },

    async loadFilters(context) {
      context.commit('LOAD_FILTERS');
    },

    async setSpecificFilter(context, data) {
      context.commit('SET_SPECIFIC_FILTER', data);
      context.commit('FILTER_LISTS');
    },

    clearSelected(context) {
      context.commit('SET_SELECTED_LISTS', []);
    }
  }
};