import moment from 'moment';

export default {
  namespaced: true,
  state: {
    statDates: {
      startDate: moment().format('YYYY-MM-DD'),
      endDate: moment().format('YYYY-MM-DD'),
    },
    aggregateColumns: [
      'clicks',
      'conversions',
      'cost_per_registration',
      'cost_per_result',
      'cpa',
      'cpc',
      'cpm',
      'ctr',
      'cvr',
      'impressions',
      'result',
      'stat_cost',
      'total_registration',
    ]
  },

  getters: {
    statDates: state => state.statDates,
    aggregateColumns: state => state.aggregateColumns,
  },
  mutations: {
    SET_STAT_DATES: (state, dates) => {
      state.statDates = dates;
    },
  },
  actions: {
    async setStatDates(context, dates) {
      context.commit('SET_STAT_DATES', dates);
    },
  }
};
