export default {
  namespaced: true,
  
  state: {
    rates: {}
  },
  
  getters: {
    rates: state => state.rates,
  },
  
  mutations: {
    SET_RATES: (state, payload) => {
      state.rates = payload;
    },
  },
  
  actions: {
    async loadRates({
      commit
    }) {
      const response = await this._vm.api.get('/usd_rates');
      commit('SET_RATES', response.data.data);
    },
  },
};