import Vue from 'vue';

export default class RestResource {

  async sendRequest(state, columns, url) {
    if (localStorage.getItem('adsmanager-filters-dates')) {
      let date = JSON.parse(localStorage.getItem('adsmanager-filters-dates'));
      url = url + 'from_date=' + date.startDate + '&';
      url = url + 'to_date=' + date.endDate + '&';
    }

    let perPage = localStorage.getItem('adsmanager-items-per-page-count') ?
      localStorage.getItem('adsmanager-items-per-page-count') : 50;

    let page = localStorage.getItem('adsmanager-items-page') ?
      localStorage.getItem('adsmanager-items-page') : 1;

    url = url + 'perPage=' + perPage + '&page=' + page;

    if (state.filters.search) {
      url = url + '&q=' + state.filters.search;
    }

    if (state.sorts && state.sorts.key) {
      if (state.sorts.key.includes('action_')) {
        state.sorts.key = state.sorts.key.replace('action_', '');
      }

      if (state.sorts.key.includes('-quantity')) {
        state.sorts.key = state.sorts.key.replace('-quantity', '');
      }

      url = url + '&sort[key][]=' + state.sorts.key;
      url = url + '&sort[value][]=' + (state.sorts.value ? state.sorts.value : 'asc');
    }

    if (state.filters['statuses']) {
      state.filters['statuses'].forEach((elem) => {
        url = url+'&status[]=' + elem;
      });
    }

    if (state.filters['accountsStatuses']) {
      state.filters['accountsStatuses'].forEach((elem) => {
        if (state.cabs || state.campaigns) {
          url = url + '&accountStatus[]=' + elem;
        } else {
          url = url+'&campaignStatus[]=' + elem;
        }
      });
    }

    if (state.filters['cabsStatuses']) {
      state.filters['cabsStatuses'].forEach((elem) => {
        url = url+'&adAccountStatus[]=' + elem;
      });
    }

    if (columns && columns.length > 0) {
      columns.forEach((elem) => {
        if (elem.includes('action_')) {
          elem = elem.replace('action_', '');
        }
        url = url+'&aggregateColumns[]=' + elem;
      });
    }

    let currency  = 'USD';

    if (localStorage.getItem('adsmanager-currency') == null ||
      localStorage.getItem('adsmanager-currency') == 'undefined'
    ) {
      localStorage.setItem('adsmanager-currency','USD');
    } else {
      currency = localStorage.getItem('adsmanager-currency');
    }

    url = url+'&currency=' + currency;

    let accounts = [];
    let cabs = [];
    let campaigns = [];
    let adsets = [];

    if (!url.startsWith('/fb-accounts')) {
      accounts = JSON.parse(localStorage.getItem('adsmanager-accounts-selected'));
    }

    if (url.startsWith('/fb-campaigns')) {
      cabs = JSON.parse(localStorage.getItem('adsmanager-cabs-selected'));
    }

    if (url.startsWith('/fb-adsets')) {
      cabs = JSON.parse(localStorage.getItem('adsmanager-cabs-selected'));
      campaigns = JSON.parse(localStorage.getItem('adsmanager-campaigns-selected'));
    }

    if (url.startsWith('/fb-ads')) {
      cabs = JSON.parse(localStorage.getItem('adsmanager-cabs-selected'));
      campaigns = JSON.parse(localStorage.getItem('adsmanager-campaigns-selected'));
      adsets = JSON.parse(localStorage.getItem('adsmanager-adsets-selected'));
    }

    if (Array.isArray(accounts) && accounts.length) {
      accounts.map(account => {
        url = url + `&accountIds[]=${account.id}&`;
      });
    }

    if (Array.isArray(cabs) && cabs.length) {
      cabs.map(cab => {
        url = url + `&adAccountIds[]=${cab.id}&`;
      });
    }

    if (Array.isArray(campaigns) && campaigns.length) {
      campaigns.map(campaign => {
        url = url + `&campaignIds[]=${campaign.id}&`;
      });
    }

    if (Array.isArray(adsets) && adsets.length) {
      adsets.map(adset => {
        url = url + `&adsetIds[]=${adset.id}&`;
      });
    }

    return Vue.prototype.cloudApi(url);
  }

  async getRules(state, url) {

    let perPage = localStorage.getItem('autorules-per-page-count') ?
      localStorage.getItem('autorules-per-page-count') : 50;

    let page = localStorage.getItem('autorules-page') ?
      localStorage.getItem('autorules-page') : 1;

    url = url + 'perPage=' + perPage + '&page=' + page;

    if (state.filters.search) {
      url = url + '&q=' + state.filters.search;
    }

    if (state.sorts.key) {
      if (state.sorts.key === 'data') {
        return ;
      }

      if (state.sorts.key.includes('action_')) {
        state.sorts.key = state.sorts.key.replace('action_', '');
      }

      if (state.sorts.key.includes('-quantity')) {
        state.sorts.key = state.sorts.key.replace('-quantity', '');
      }

      url = url + '&sort[key][]=' + state.sorts.key;
      url = url + '&sort[value][]=' + state.sorts.value ? state.sorts.value : 'asc';
    }

    return Vue.prototype.cloudApi(url);
  }
  
  async getProxies(state, url) {

    let perPage = localStorage.getItem('proxies-items-per-page-count') ?
      localStorage.getItem('proxies-items-per-page-count') : 50;

    let page = localStorage.getItem('proxies-page') ?
      localStorage.getItem('proxies-page') : 1;

    url = url + 'perPage=' + perPage + '&page=' + page;

    if (state.filters.search) {
      url = url + '&q=' + state.filters.search;
    }

    if (state.sorts.key) {
      if (state.sorts.key === 'data') {
        return;
      }
      url = url + '&sort[key][]=' + state.sorts.key;
      url = url + '&sort[value][]=' + state.sorts.value ? state.sorts.value : 'asc';
    }

    return Vue.prototype.cloudApi(url);
  }
}
