import { getField, updateField }      from 'vuex-map-fields';

import i18n                           from '../../i18n';
import {
  mixinDialogMutations,
  mixinDialogActions,
  mixinDialogGetters
} from '../../mixins/vuex_dialogs';

export default {
  namespaced: true,
  state: {
    rules: {
      all: [],
      filtered: [],
      selected: localStorage.getItem('autorules-rules-selected') ?
        JSON.parse(localStorage.getItem('autorules-rules-selected')) : [],
      forMainDialog: {
        name: null,
        evaluation_type: 'SCHEDULE',
        schedule_type: 'DAILY',
        entity_type: 'AD',
        effective_status: 'ACTIVE',
        execution_type: 'PAUSE',
        time_preset: 'TODAY',
        conditions: [    
          {
            // parameter: 'impressions', 
            operator: 'EQUAL',
            value: null
          }
        ],
        custom_rasp: {
          days: [],
          main_time: {
            hours: '00',
            mins: '00'
          },
        },
        execution: {
          bid_change_amount: null,
          bid_max: null,
          bid_min: null,
          bid_operator: "+",
          bid_percents: null,
          budget_change_amount: null,
          budget_max: null,
          budget_min: null,
          budget_operator: "+",
          budget_percents: null
        },
        convert_currency: false,
        currency: 'USD',
        budget_type: 'daily'
      },
      count: 0,
      itemsCount: 0,
      page: 1
    },
    emptyFilters: {
      search: ''
    },
    filters: {},
    loading: {
      deleteRule: false,
      createOrSaveRule: false,
      mainTable: false,
      share: false
    },
    dialogs: {
      mainDialog: false,
      share: false
    },
    mainDialogMode: 'create',
    showWarnings: false,
    sorts: [],
  },

  getters: {
    ...mixinDialogGetters,
    getField,

    rules: state => state.rules,
    filters: state => state.filters,
    loading: state => state.loading,
    mainDialogMode: state => state.mainDialogMode,
    showWarnings: state => state.showWarnings,
    count: state => state.rules.count,
    page: state => state.rules.page,
    itemsCount: state => state.rules.itemsCount,
    selected: state => state.rules.selected,

  },
  
  mutations: {
    ...mixinDialogMutations,
    updateField,

    SET_ALL: (state, payload) => {
      state.rules.all = payload;
    },

    FILTER: (state, payload) => {
      state.rules.filtered = payload;
    },

    COUNT: (state, payload) => {
      state.rules.count = payload;
    },

    ITEMS_COUNT: (state, payload) => {
      state.rules.itemsCount = payload;
    },

    SET_PAGE: (state, payload) => {
      state.rules.page = payload;
    },

    SET_SELECTED: (state, data) => {
      state.rules.selected = data;
      localStorage.setItem('autorules-rules-selected', JSON.stringify(data));
    },

    SET_SPECIFIC_FILTER: (state, data) => {
      state.filters[data.filter] = data.data;
      localStorage.setItem('autorules-rules-filters', JSON.stringify(state.filters));
    },

    LOAD_FILTERS: (state) => {
      if (localStorage.getItem('autorules-rules-filters')) {
        state.filters = JSON.parse(localStorage.getItem('autorules-rules-filters'));
      } else {
        state.filters = {...state.emptyFilters};
      }
    },

    SET_LOADING: (state, data) => {
      state.loading[data.param] = data.value;
    },

    SET_MAIN_DIALOG_MODE: (state, data) => {
      state.mainDialogMode = data;
    },

    SET_FOR_MAIN_DIALOG: (state, data) => {
      data.execution.bid_operator = data.execution.bid_change_amount < 0 ? '-' : '+';
      data.execution.bid_change_amount = Math.abs(data.execution.bid_change_amount);
      state.rules.forMainDialog = data;
    },

    SET_WARNING: (state, payload) => {
      state.showWarnings = payload;
    },

    SET_FOR_MAIN_DIALOG_OPTIONS: (state, payload) => {
      state.rules.forMainDialog[payload.option] = payload.value;
    },

    SET_FOR_MAIN_DIALOG_EXECUTION_OPTIONS: (state, payload) => {
      state.rules.forMainDialog.execution[payload.option] = payload.value;
    },
  },
  
  actions: {
    ...mixinDialogActions,

    async loadRules({commit}) {
      const response = await this._vm.api.get('/fb_rules');

      if (response.status === 200) {
        commit('SET_ALL', response.data.data);
        commit('FILTER', response.data.data);
        commit('COUNT', response.data.meta.last_page);
        commit('ITEMS_COUNT', response.data.meta.total);
        commit('SET_PAGE', response.data.meta.current_page);
      }
    },

    setSelected(context, payload) {
      context.commit('SET_SELECTED', payload);
    },

    clearSelected(context) {
      context.commit('SET_SELECTED', []);
    },

    setSpecificFilter(context, data) {
      context.commit('SET_SPECIFIC_FILTER', data);
      context.commit('filterRules');
      context.dispatch('loadRules');

    },

    loadFilters(context) {
      context.commit('LOAD_FILTERS');
    },

    async deleteRule(context, ids) {
      context.commit('SET_LOADING', {
        param: 'deleteRule',
        value: true
      });

      const response = await this.api.delete('/fb_rules', { params: { ids } }).catch((e) => {
        context.dispatch('main/apiError', e, {
          root: true
        });
      });

      context.commit('SET_LOADING', {
        param: 'deleteRule',
        value: false
      });

      if ((response.status >= 200 || response.status < 300)) {
        context.dispatch('main/alert', {
          color: 'success',
          message: i18n.t('common.deleted')
        }, {
          root: true
        });
        context.dispatch('loadRules');
        context.dispatch('clearSelected');
      } else {
        context.dispatch('main/apiError', response.data, {
          root: true
        });
      }

      return response.data.success;
    },

    setMainDialogMode(context, data) {
      context.commit('SET_MAIN_DIALOG_MODE', data);
    },

    setForMainDialog(context, data) {
      context.commit('SET_FOR_MAIN_DIALOG', data);
    },

    async createRule(context) {
      let data = {...context.state.rules.forMainDialog};
      data.convert_currency = +context.state.rules.forMainDialog.convert_currency;
      data.custom_rasp.days = data.custom_rasp.days.filter(day => day !== 'all');

      context.commit('SET_LOADING', {
        param: 'createOrSaveRule',
        value: true
      });

      const response = await this._vm.api.post('/fb_rules', data).catch((e) => {
        context.dispatch('main/apiError', e, {
          root: true
        });
      });

      context.commit('SET_LOADING', {
        param: 'createOrSaveRule',
        value: false
      });

      if (response.status === 200 && response.data) {
        context.dispatch('main/alert', {
          color: 'success',
          message: i18n.t('common.created')
        }, {
          root: true
        });
        context.dispatch('clearRule');
        context.dispatch('closeDialog', 'mainDialog');
        context.dispatch('loadRules');
      } else {
        context.dispatch('main/apiError', response.data, {
          root: true
        });
      }

      return response.data.success;
    },

    async editRule(context) {
      let data = {...context.state.rules.forMainDialog};
      data.convert_currency = +context.state.rules.forMainDialog.convert_currency;

      if (data.custom_rasp) {
        data.custom_rasp.days = data.custom_rasp.days.filter(day => day !== 'all');
      }

      context.commit('SET_LOADING', {
        param: 'createOrSaveRule',
        value: true
      });

      const response = await this.api.put(`/fb_rules/${data.id}`, data).catch((e) => {
        context.dispatch('main/apiError', e, {
          root: true
        });
      });

      context.commit('SET_LOADING', {
        param: 'createOrSaveRule',
        value: false
      });

      if (response.status === 200 && response.data) {
        context.dispatch('main/alert', {
          color: 'success',
          message: i18n.t('common.created')
        }, {
          root: true
        });
        context.dispatch('clearRule');
        context.dispatch('closeDialog', 'mainDialog');
        context.dispatch('loadRules');
      } else {
        context.dispatch('main/apiError', response.data, {
          root: true
        });
      }

      return response.data;
    },
    
    setWarning(context, data) {
      context.commit('SET_WARNING', data);
    },
    
    setLoading(context, data) {
      context.commit('SET_LOADING', data);
    },
    
    clearRule(context) {
      let rule = {
        name: null,
        evaluation_type: 'SCHEDULE',
        schedule_type: 'DAILY',
        entity_type: 'AD',
        effective_status: 'ACTIVE',
        execution_type: 'PAUSE',
        time_preset: 'TODAY',
        conditions: [    
          {
            // parameter: 'impressions', 
            operator: 'EQUAL',
            value: null
          }
        ],
        custom_rasp: {
          days: [],
          main_time: {
            hours: '00',
            mins: '00'
          },
        },
        execution: {
          bid_change_amount: null,
          bid_max: null,
          bid_min: null,
          bid_operator: "+",
          bid_percents: null,
          budget_change_amount: null,
          budget_max: null,
          budget_min: null,
          budget_operator: "+",
          budget_percents: null
        },
        budget_type: 'daily',
        convert_currency: false,
        currency: 'USD'
      };
      context.commit('SET_FOR_MAIN_DIALOG', rule);
    },

    async multipleDuplicate(context, data) {
      let payload = {
        rule_id: data
      };
      const response = await this._vm.api.post('/fb_rules/duplicate_rule', payload).catch((e) => {
        context.dispatch('main/apiError', e, {
          root: true
        });
      });

      if (response.data.success) {
        context.dispatch('main/alert', {
          color: 'success',
          message: i18n.t('common.duplicated')
        }, {
          root: true
        });
      } else {
        context.dispatch('main/apiError', response.data, {
          root: true
        });
      }
      return response.data.success;
    },

    setForMainDialogOptions(context, data) {
      context.commit('SET_FOR_MAIN_DIALOG_OPTIONS', data);
    },

    setForMainDialogExecutionOptions(context, data) {
      context.commit('SET_FOR_MAIN_DIALOG_EXECUTION_OPTIONS', data);
    },

    setPage({commit}, data) {
      commit('SET_PAGE', data);
    },

    async clear(context) {
      context.commit('SET_ALL', []);
      context.commit('SET_FILTERED', []);
      context.commit('SET_SELECTED', []);
    },

    async sortRules(context, data) {
      context.commit('SORT', data);
      await context.dispatch('loadRules');
    },
  }
};