<template>
  <v-dialog
    :value="dialogs.feedback"
    width="600"
    scrollable
    @click:outside="$store.dispatch('main/closeDialog', 'feedback')"
    @keydown.esc="$store.dispatch('main/closeDialog', 'feedback')"
  >
    <v-card>
      <!-- ЗАГОЛОВОК -->
      <v-toolbar
        :height="48"
        dark
        color="primary"
        class="mb-5"
      >
        <v-toolbar-title>
          {{ $t('sidebar.feedback') }}
        </v-toolbar-title>

        <v-spacer />

        <v-toolbar-items>
          <v-btn
            icon
            @click="$store.dispatch('main/closeDialog', 'feedback')"
          >
            <v-icon>
              fas fa-times
            </v-icon>
          </v-btn>
        </v-toolbar-items>
      </v-toolbar>

      <v-card-text>
        <v-container class="pb-0">
          <!-- ТИПЫ ОБРАТНОЙ СВЯЗИ -->
          <v-btn-toggle
            v-model="type"
            mandatory
          >
            <v-btn
              value="bug"
              small
              color="primary"
              outlined
            >
              <span class="mr-1">
                <v-icon
                  size="14"
                  color="primary"
                >
                  fas fa-bug
                </v-icon>
              </span>
              {{ $t('dialogs.feedback.bug') }}
            </v-btn>
            <v-btn
              value="idea"
              small
              color="primary"
              outlined
            >
              <span class="mr-1">
                <v-icon
                  size="14"
                  color="primary"
                >
                  fas fa-lightbulb
                </v-icon>
              </span>
              {{ $t('dialogs.feedback.idea') }}
            </v-btn>
            <v-btn
              value="review"
              small
              color="primary"
              outlined
            >
              <span class="mr-1">
                <v-icon
                  size="14"
                  color="primary"
                >
                  far fa-file-alt
                </v-icon>
              </span>
              {{ $t('dialogs.feedback.review') }}
            </v-btn>
            <v-btn
              value="feature"
              small
              color="primary"
              outlined
            >
              <span class="mr-1">
                <v-icon
                  size="14"
                  color="primary"
                >
                  fas fa-puzzle-piece
                </v-icon>
              </span>
              {{ $t('dialogs.feedback.feature') }}
            </v-btn>
          </v-btn-toggle>

          <!-- СРОЧНЫЙ ВОПРОС -->
          <v-alert
            v-show="['bug', 'feature'].includes(type)"
            text
            color="orange"
            class="mb-0 mt-3"
            icon="fas fa-exclamation-circle"
            style="font-size: 14px;"
          >
            <span>{{ $t('dialogs.sidebar.urgentQuestion') + ' ' }}</span>
            <a
              target="_blank"
              href="https://t.me/dolphin_support_bot"
            >{{ $t('dialogs.sidebar.support') }}</a>
          </v-alert>
        </v-container>
        <v-container>
          <!-- СООБЩЕНИЕ -->
          <v-textarea
            v-model="content"
            dense
            outlined
            hide-details
            auto-grow
          />
        </v-container>
      </v-card-text>

      <v-divider />

      <v-card-actions>
        <v-spacer />
        <v-btn
          text
          color="grey"
          @click="$store.dispatch('main/closeDialog', 'feedback')"
        >
          {{ $t('common.close') }}
        </v-btn>
        <v-btn
          text
          color="primary"
          :loading="loading"
          @click="sendFeedback"
        >
          {{ $t('common.send') }}
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import axios                     from 'axios';
import Cookies                   from 'js-cookie';
import { mapGetters }            from 'vuex';

export default {
  name: 'SidebarDialogFeedback',
  data () {
    return {
      type: 'feature',
      content: '',
      loading: false
    };
  },
  computed: {
    ...mapGetters({
      dialogs: 'main/dialogs',
      profile: 'main/profile'
    })
  },
  methods: {
    async sendFeedback () {
      this.loading = true;

      let panel;

      if (location.hostname === 'localhost' || location.hostname.search('95.217.13.15') > -1) {
        panel = axios.create({
          baseURL: 'https://panel.dolphin.ru.com',
          headers: {
            Authorization: 'bearer eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..bBBpb5MfC7mn9XN8PmDQT8-i_q_BKxFebWp4IDPHDRU',
          },
        });
      } else {
        panel = axios.create({
          baseURL: 'https://panel.dolphin.ru.com',
          headers: {
            Authorization: `${Cookies.get('user_id')}-${Cookies.get('hash')}`,
          },
        });
      }

      await panel.post(`/?c=feedback&lic=${this.profile.lic.trim()}`, {
        type: this.type,
        content: this.content
      });
      this.$store.dispatch('main/closeDialog', 'feedback');

      this.loading = false;
      this.$store.dispatch('main/alert', { color: 'success', message: this.$t('dialogs.feedback.feedbackSent') });
    }
  }
};
</script>
