import '@fortawesome/fontawesome-free/css/all.css';
import 'roboto-fontface/css/roboto/roboto-fontface.css';
import 'vue2-daterange-picker/dist/vue2-daterange-picker.css';
import axios     from 'axios';
import Echo      from 'laravel-echo';
import _         from 'lodash';
import moment    from 'moment';
import Clipboard from 'v-clipboard';
import FlagIcon  from 'vue-flag-icon';
import Vuelidate from 'vuelidate';
import Vuex      from 'vuex';
import Vue       from 'vue';

import App       from './App.vue';
import i18n      from './i18n';
import apiError  from './mixins/api_error_root';
import vuetify   from './plugins/vuetify';
import router    from './router/index';
import store     from './store/index';

Object.defineProperty(Vue.prototype, '$_', { value: _ });

Vue.prototype.moment = moment;
if (localStorage.getItem('language')) {
  switch(localStorage.getItem('language')) {
  case 'en':
    Vue.prototype.moment.locale('en-gb');
    break;
  case 'cn':
    Vue.prototype.moment.locale('zh-cn');
    break;
  default:
    Vue.prototype.moment.locale('ru');
    break;
  }
}

let api, cloudApi, baseUrl, echo;

if (location.hostname === 'localhost' || location.hostname.search('surge.sh') > -1) {
  baseUrl = 'https://cloud-dev.dolphin.ru.com';
} else if(location.hostname.search('app.dolphin') > -1) {
  baseUrl = 'https://app.dolphin.ru.com';
} else {
  baseUrl = `${location.protocol}//${location.hostname}`;
}

api = axios.create({
  baseURL: `${baseUrl}/api/v1`,
  headers: {
    Authorization: `Bearer ${localStorage.getItem('apiToken')}`
  },
});

cloudApi = axios.create({
  baseURL: `${baseUrl}/api/v1`,
  headers: {
    Authorization: `Bearer ${localStorage.getItem('apiToken')}`
  },
});

window.io = require('socket.io-client');

echo = new Echo({
  broadcaster: 'socket.io',
  host: 'https://dev-cloud-sockets.dolphin.ru.com',
  headers: {
    'Authorization' : `Bearer ${localStorage.getItem('apiToken')}`,
  },
});
Vue.prototype.cloudApi = cloudApi;
Vue.prototype.api = api;
Vue.prototype.apiError = apiError;
Vue.prototype.baseUrl = baseUrl;
Vue.prototype.echo = echo;

store.api = api;

Vue.use(Vuex);
Vue.use(Clipboard);
Vue.use(FlagIcon);
Vue.use(Vuelidate);

new Vue({
  vuetify,
  i18n,
  router,
  store,
  api,
  render: (h) => h(App),
}).$mount('#app');
