export const mixinMainTableState = {
  mainTableData: [],
  mainTablePagination: {
    per_page: 30
  },
  mainTableLoading: false,
  sorts: {},
};

export const mixinMainTableMutations = {
  SET_MAIN_TABLE_DATA: (state, payload) => {
    state.mainTableData = payload;
  },

  SET_MAIN_TABLE_PAGINATION: (state, payload) => {
    state.mainTablePagination = payload;
  },

  SET_MAIN_TABLE_LOADING: (state, payload) => {
    state.mainTableLoading = payload;
  },

  SET_MAIN_TABLE_SORTS: (state, payload) => {
    state.mainTableSorts = payload;
  },

  CLEAR_SELECTED: (state) => {
    state.selected = [];
  },

  REPLACE_SELECTED: (state, payload) => {
    state.selected = [];
    for (const id in payload) {
      state.mainTableData.map((item) => {
        if (item.id in payload[id]) {
          state.selected.push(item);
        }
      });
    }
  },

  SORT: (state, data) => {
    state.sorts = {};
    if (data.key !== 'none') {
      state.sorts['key'] = data.key;
      state.sorts['value'] = data.value;
    }
  },

};

const endpointsForPath = [
  'tiktok/adaccounts',
  'tiktok/campaigns',
  'tiktok/adsets',
  'tiktok/creatives',
];

export const mixinMainTableActions = {
  async setCommonQueries({ state, rootState }, data) {
    const {
      endpoint,
      page,
      itemsPerPage,
      query,
      orderBy,
      orderType,
      startDate,
      endDate,
      aggregateColumns
    } = data;
    const { page: statePage, per_page } = state.mainTablePagination;
    const { statDates, aggregateColumns: stateAggregateColumns } = rootState.tiktokAdsmanager;

    let path = `/${endpoint}?`;

    if (aggregateColumns) {
      for (const aggregateColumn of aggregateColumns) {
        path += `aggregateColumns[]=${aggregateColumn}&`;
      }
    } else if (stateAggregateColumns) {
      for (const aggregateColumn of stateAggregateColumns) {
        path += `aggregateColumns[]=${aggregateColumn}&`;
      }
    }
    if (startDate && endDate) {
      path += `from_date=${startDate}&to_date=${endDate}&`;
    } else if (statDates.startDate && statDates.endDate) {
      path += `from_date=${statDates.startDate}&to_date=${statDates.endDate}&`;
    }
    if (page) {
      path += `page=${page}&`;
    } else if (statePage) {
      path += `page=${statePage}&`;
    }
    if(orderBy && orderBy !== 'none' && orderType && orderType !== 'none') {
      path += `sort[key][]=${orderBy}&sort[value][]=${orderType}&`;
    } else if (state.sorts.key && state.sorts.value) {
      path += `sort[key][]=${state.sorts.key}&sort[value][]=${state.sorts.value}&`;
    }
    if (itemsPerPage) {
      path += `perPage=${itemsPerPage}&`;
    } else if (per_page) {
      path += `perPage=${per_page}&`;
    }
    if (query) {
      path += `q=${query}&`;
    }

    return path;
  },

  async setSpecialQueries({ rootState }, path) {
    const { selected: selectedAccounts } = rootState.tiktokAdsmanagerAccounts;
    if (Array.isArray(selectedAccounts) && selectedAccounts.length) {
      for (const account of selectedAccounts) {
        path += `accountIds[]=${account.id}&`;
      }
    }
    const { selected: selectedAdAccounts } = rootState.tiktokAdsmanagerAdAccounts;
    if (Array.isArray(selectedAdAccounts) && selectedAdAccounts.length) {
      for (const adAccount of selectedAdAccounts) {
        path += `adAccountIds[]=${adAccount.id}&`;
      }
    }

    const { selected: selectedCampaigns } = rootState.tiktokAdsmanagerCampaigns;
    if (Array.isArray(selectedCampaigns) && selectedCampaigns.length) {
      for (const campaign of selectedCampaigns) {
        path += `campaignIds[]=${campaign.id}&`;
      }
    }

    const { selected: selectedAdsets } = rootState.tiktokAdsmanagerAdsets;
    if (Array.isArray(selectedAdsets) && selectedAdsets.length) {
      for (const adset of selectedAdsets) {
        path += `adsetIds[]=${adset.id}&`;
      }
    }

    return path;
  },

  async loadMainTableData({ commit, dispatch }, data) {
    try {
      commit('SET_MAIN_TABLE_LOADING', true);

      const { endpoint, query } = data;
      let path = await dispatch('setCommonQueries', data);

      if (endpointsForPath.includes(endpoint)) {
        path = await dispatch('setSpecialQueries', path);
      }

      if (path[path.length - 1] === '&' || path[path.length - 1] === '?') {
        path = path.slice(0, path.length - 1);
      }

      const response = await this._vm.api(path);
      const { status, data: responseData } = response;
      
      if (status === 200 && responseData && Array.isArray(responseData.data)) {
        commit('SET_MAIN_TABLE_DATA', responseData.data);
        if (responseData.meta) {
          commit('SET_MAIN_TABLE_PAGINATION', {
            ...responseData.meta,
            query: query ? query : ''
          });
        } else {
          dispatch('setPaginationFromData', {
            ...responseData,
            query: query ? query : ''
          });
        }
      } else {
        throw new Error('invalid response');
      }
    } catch (error) {
      // 
    } finally {
      commit('SET_MAIN_TABLE_LOADING', false);
    }
  },

  async setMainTableLoading({ commit }, data) {
    commit('SET_MAIN_TABLE_LOADING', data);
  },

  async setMainTablePagination({ commit }, data) {
    commit('SET_MAIN_TABLE_PAGINATION', data);
  },

  async clearSelected({ commit }) {
    commit('CLEAR_SELECTED');
  },

  async replaceSelected({ commit }, data) {
    commit('REPLACE_SELECTED', data);
  },

  async setPaginationFromData({ commit }, data) {
    const { 
      current_page,
      per_page,
      from,
      to,
      last_page,
      total } = data;

    const pagination = {
      query: data.query,
      current_page,
      per_page,
      from,
      to,
      last_page,
      total
    };
    commit('SET_MAIN_TABLE_PAGINATION', pagination);
  },
};

export const mixinMainTableGetters = {
  mainTableData: state => state.mainTableData,
  mainTableLoading: state => state.mainTableLoading,
  mainTablePagination: state => state.mainTablePagination,
};
