import i18n                           from '../../i18n';
import {
  mixinDialogMutations,
  mixinDialogActions,
  mixinDialogGetters
} from '../../mixins/vuex_dialogs';

export default {
  namespaced: true,
  state: {
    conversions: {
      all: [],
      selected: []
    },
    loading: {
      deleteConversion: false
    },
    dialogs: {
      dialogImport: false
    }
  },

  getters: {
    ...mixinDialogGetters,

    conversions: state => state.conversions,
    loading: state => state.loading,
  },
  
  mutations: {
    ...mixinDialogMutations,

    SET_CONVERSIONS: (state, payload) => {
      state.conversions.all = payload;
    },

    SET_SELECTED: (state, data) => {
      state.conversions.selected = data;
      localStorage.setItem('tracker-conversions-selected', JSON.stringify(data));
    },

    SET_LOADING: (state, data) => {
      state.loading[data.param] = data.value;
    },
  },
  
  actions: {
    ...mixinDialogActions,
    
    async loadConversions({commit}) {
      const response = await this._vm.api.get('/tracker/conversions');
      if (typeof response.data.data !== 'undefined') {
        commit('SET_CONVERSIONS', response.data.data);
      }
    },

    setSelected(context, payload) {
      context.commit('SET_SELECTED', payload);
    },

    clearSelected(context) {
      context.commit('SET_SELECTED', []);
    },

    async deleteConversion(context, data) {
      context.commit('SET_LOADING', {
        param: 'deleteConversion',
        value: true
      });

      const response = await this._vm.api.post('/tracker/delete_conversions', data).catch((e) => {
        context.dispatch('main/apiError', e, {
          root: true
        });
      });

      context.commit('SET_LOADING', {
        param: 'deleteConversion',
        value: false
      });

      if (response.data.success) {
        context.dispatch('main/alert', {
          color: 'success',
          message: i18n.t('common.deleted')
        }, {
          root: true
        });
        context.dispatch('loadConversions');
        context.dispatch('clearSelected');
      } else {
        context.dispatch('main/apiError', response.data, {
          root: true
        });
      }

      return response.data.success;
    },

    async importConversions(context, data) {
      context.commit('SET_LOADING', {
        param: 'importConversions',
        value: true
      });

      let payload = {
        raw_data: data
      };

      const response = await this._vm.api.post('/tracker/import_conversions', payload).catch((e) => {
        context.dispatch('main/apiError', e, {
          root: true
        });
      });

      context.commit('SET_LOADING', {
        param: 'importConversions',
        value: false
      });

      if (response.data.success) {
        context.dispatch('main/alert', {
          color: 'success',
          message: i18n.t('common.imported')
        }, {
          root: true
        });
        context.dispatch('loadConversions');
      } else {
        context.dispatch('main/apiError', response.data, {
          root: true
        });
      }

      return response.data.success;
    },
  }
};
