import { PLAN_TYPE_PRO, SUBSCRIPTION_STATUS_EXPIRED } from '@/constants/tiktok/personalArea/payment/payment';
import store                                          from '@/store';
import VueRouter                                      from 'vue-router';
import Vue                                            from 'vue';

Vue.use(VueRouter);

const hasActiveSubscription = async () => {
  if (!store.state.main.profile.subscription) {
    await store.dispatch('main/loadProfile');
  }

  return store.state.main.profile.subscription.status !== SUBSCRIPTION_STATUS_EXPIRED;
};

const tiktokBeforeEnter = async (to, from, next) => {
  if (localStorage.getItem('apiToken')) {
    let subscriptionActive = await hasActiveSubscription();

    if (to.path === '/tiktok/personalArea') {
      return next();
    }

    if (!subscriptionActive || (to.name === 'TiktokCreateAds'
        && store.state.main.profile.subscription.plan.name !== PLAN_TYPE_PRO)
    ) {
      return next('/tiktok/personalArea');
    }

    return next();
  }

  if (location.hostname.search('localhost') > -1) {
    return next('/auth/login');
  } else {
    window.location.href = '/start';
  }
};

const ifAuthenticated = (to, from, next) => {
  if (localStorage.getItem('apiToken')) {
    return next();
  }

  if(location.hostname.search('localhost') > -1) {
    return next('/auth/login');
  } else {
    window.location.href = '/start';
  }
};

const ifNotAuthenticated = (to, from, next) => {
  if (!localStorage.getItem('apiToken')) {

    if(location.hostname.search('localhost') > -1) {
      return next();
    } else {
      window.location.href = '/start';
    }
  }
  return next('/');
};

const routes = [
  {
    path: '/',
    redirect: { name: 'Tiktok' }
  },
  {
    path: '/adsmanager',
    component: () => import( /* webpackChunkName: "adsmanager" */ '../views/adsmanager/AdsManager.vue'),
    name: 'Adsmanager',
  },
  {
    path: '/proxy',
    component: () => import( /* webpackChunkName: "proxy" */ '../views/Proxy.vue'),
    beforeEnter: ifAuthenticated
  }, 
  {
    path: '/browserExtension',
    component: () => import( /* webpackChunkName: "browserExtension" */ '../views/BrowserExtension.vue')
  },
  {
    path: '/settings',
    component: () => import( /* webpackChunkName: "settings" */ '../views/Settings.vue')
  },
  {
    path: '/api',
    component: () => import( /* webpackChunkName: "api" */ '../views/Api.vue')
  },
  {
    path: '/upload/:tab',
    component: () => import( /* webpackChunkName: "upload" */ '../views/Upload.vue'),
  },
  {
    path: '/fbrules/:tab',
    component: () => import( /* webpackChunkName: "autorules" */ '../views/AutoRules.vue'),
  },
  {
    path: '/tracker/:tab',
    component: () => import( /* webpackChunkName: "tracker" */ '../views/Tracker.vue'),
  },
  {
    path: '/comments/:tab',
    component: () => import( /* webpackChunkName: "comments" */ '../views/Comments.vue'),
  },
  {
    path: '/bankcards',
    component: () => import( /* webpackChunkName: "bankcards" */ '../views/BankCards.vue'),
  },
  {
    path: '/news',
    component: () => import( /* webpackChunkName: "news" */ '../views/News.vue'),
  },
  {
    path: '/anty',
    component: () => import( /* webpackChunkName: "news" */ '../views/Anty.vue'),
  },
  {
    path: '/tiktok',
    name: 'Tiktok',
    redirect: () => {
      // the function receives the target route as the argument
      // we return a redirect path/location here.
      return { path: '/tiktok/adsmanager/accounts'} ;
    },
    beforeEnter: tiktokBeforeEnter
  },
  {
    path: '/tiktok/proxy',
    component: () => import( /* webpackChunkName: "proxy" */ '../views/Proxy.vue'),
    beforeEnter: tiktokBeforeEnter

  },
  {
    path: '/tiktok/personalArea',
    component: () => import( /* webpackChunkName: "proxy" */ '../views/PersonalArea'),
    beforeEnter: tiktokBeforeEnter
  },
  {
    path: '/tiktok/create-ads/:tab',
    name: 'TiktokCreateAds',
    component: () => import('../views/tiktok/createAds/TiktokCreateAds.vue'),
    beforeEnter: tiktokBeforeEnter

  },
  {
    path: '/tiktok/adsmanager',
    name: 'TiktokAdsmanager',
    redirect: () => {
      // the function receives the target route as the argument
      // we return a redirect path/location here.
      return { path: '/tiktok/adsmanager/accounts'} ;
    },
    beforeEnter: tiktokBeforeEnter
  },
  {
    path: '/tiktok/adsmanager/:tab',
    name: 'TiktokAdsmanagerTab',
    component: () => import('../views/tiktok/adsmanager/TiktokAdsManager.vue'),
    beforeEnter: tiktokBeforeEnter
  },
  {
    path: '/tiktok/user-apps',
    name: 'TiktokUserApps',
    component: () => import('../views/tiktok/TiktokUserApps.vue'),
    beforeEnter: tiktokBeforeEnter
  },
  {
    path: '/auth/login',
    name: 'AuthLogin',
    component: () => import('../views/auth/Login.vue'),
    beforeEnter: ifNotAuthenticated
  },
  {
    path: '/auth/registration',
    name: 'AuthRegistration',
    component: () => import('../views/auth/Registration.vue'),
    beforeEnter: ifNotAuthenticated
  },
];

const router = new VueRouter({
  routes
});

export default router;
