import {
  mixinDialogMutations,
  mixinDialogActions,
  mixinDialogGetters
} from '../../mixins/vuex_dialogs';

export default {
  namespaced: true,
  state: {
    activeTab: {
      tab: 0,
      component: 'bundles'
    },
    dialogs: {},
  },
  getters: {
    ...mixinDialogGetters,

    activeTab: (state) => state.activeTab,
    bundles: state => state.bundles,
  },
  mutations: {
    ...mixinDialogMutations,

    SET_ACTIVE_TAB: (state, tab) => {
      state.activeTab = tab;
      localStorage.setItem('upload-active-tab', JSON.stringify(tab));
    },
  },
  actions: {
    ...mixinDialogActions,

    async setActiveTab(context, tab) {
      context.commit('SET_ACTIVE_TAB', tab);
    },
  },
};