import moment from 'moment';

import {
  mixinDialogMutations,
  mixinDialogActions,
  mixinDialogGetters
} from '../../mixins/vuex_dialogs';

const storedDates = localStorage.getItem('comments-filters-dates');

export default {
  namespaced: true,
  state: {
    activeTab: {
      tab: 0,
      component: 'feed'
    },
    dialogs: {
      dialogWhatIsTriggerWords: false
    },
    comments: {
      all: []
    },
    filters: {
      dates: storedDates ? JSON.parse(storedDates) : {
        startDate: moment().format('YYYY-MM-DD'),
        endDate: moment().format('YYYY-MM-DD'),
      },
      accounts: [],
      tags: []
    },
    settings: {
      process: 0,
      interval: 10,
      mode: 'delete_all',
      triggerWords: null,
      triggerWordsInclude: 'exclude',
    },
    loading: {
      fbComments: false,
      settings: false,
      accounts: false,
    },
    fbComments: [],
    meta: {
      currentPage: 1,
      last_page: 1,
      isLastPage: false,
    },
    accounts: {
      list: [],
      meta: {
        currentPage: 1,
        last_page: 1,
        isLastPage: false,
      }
    }
  },
  getters: {
    ...mixinDialogGetters,

    activeTab: (state) => state.activeTab,
    filters: (state) => state.filters,
    settings: (state) => state.settings,
    loading: (state) => state.loading,
    fbComments: (state) => state.fbComments,
    meta: (state) => state.meta,
    accounts: (state) => state.accounts,
  },
  mutations: {
    ...mixinDialogMutations,

    SET_ACTIVE_TAB: (state, tab) => {
      state.activeTab = tab;
      localStorage.setItem('comments-active-tab', JSON.stringify(tab));
    },

    SET_SETTINGS: (state, data) => {
      state.settings = data;
    },
    
    SET_SETTINGS_PROPERTY: (state, { key, value }) => {
      state.settings[key] = value;
    },

    SET_COMMENTS: (state, payload) => {
      state.comments.all = payload;
    },

    ADD_FB_COMMENTS: (state, payload) => {
      state.fbComments = state.fbComments.concat(payload);
    },

    SET_FB_COMMENTS: (state, payload) => {
      state.fbComments = payload;
    },

    SET_FILTERS_DATES: (state, dates) => {
      state.filters.dates = dates;
    },

    SET_FILTERS_ACCOUNT: (state, payload) => {
      state.filters.accounts[payload.key] = payload.value;
    },

    SET_FILTERS_TAGS: (state, payload) => {
      state.filters.tags = payload;
    },

    CLEAR_FILTERS: state => {
      state.filters.accounts.selected = [];
    },

    SET_LOADING: (state, { key, value }) => {
      state.loading[key] = value;
    },

    REPLACE_FB_COMMENT: (state, { index, comment }) => {
      state.fbComments.splice(index, 1, comment);
    },

    SET_META: (state, { current_page, last_page }) => {
      state.meta = {
        currentPage: current_page,
        last_page: last_page,
        isLastPage: current_page >= last_page,
      };
    },

    ADD_ACCOUNTS: (state, payload) => {
      state.accounts.list = state.accounts.list.concat(payload);
    },

    SET_ACCOUNTS: (state, payload) => {
      state.accounts.list = payload;
    },

    SET_ACCOUNTS_META: (state, { current_page, last_page }) => {
      state.accounts.meta = {
        currentPage: current_page,
        last_page: last_page,
        isLastPage: current_page >= last_page,
      };
    },
  },
  actions: {
    ...mixinDialogActions,

    async setActiveTab(context, tab) {
      context.commit('SET_ACTIVE_TAB', tab);
    },

    async loadSettings({ dispatch, commit, rootState }) {
      commit('SET_LOADING', { key: 'settings', value: true });

      try { 
        if (!rootState.main.profile.id) {
          await dispatch('main/loadProfile', null, { root: true });
        }
        const { status, data } = await this._vm.api.get(`/fb-comment-settings/${rootState.main.profile.id}`);
  
        if (status === 200 && data && data.data) {
          commit('SET_SETTINGS', data.data);
        }
      } catch (error) {
        // 
      } finally {
        commit('SET_LOADING', { key: 'settings', value: false });
      }
    },

    async loadFbComments({ commit, state }, nextPage = false) {
      try {
        commit('SET_LOADING', { key: 'fbComments', value: true });

        let path = '/fb-comments?';
        const { dates, accounts } = state.filters;

        if (nextPage) {
          path += `page=${state.meta.currentPage + 1}&`;
        }

        if (Array.isArray(accounts) && accounts.length)  {
          for (const account of accounts) {
            path += `accountIds[]=${account.id}&`;
          }
        }

        path += `date_from=${dates.startDate}&date_to=${dates.endDate}`;

        const { status, data } = await this._vm.api(path);
  
        if (status === 200 && data && Array.isArray(data.data)) {
          if (nextPage) {
            commit('ADD_FB_COMMENTS', data.data);
          } else {
            commit('SET_FB_COMMENTS', data.data);
          }
          commit('SET_META', data.meta);
        }
      } catch (error) {
        // 
      } finally {
        commit('SET_LOADING', { key: 'fbComments', value: false });
      }
    },

    async loadAccounts({ commit, state }, nextPage = false) {
      try {
        commit('SET_LOADING', { key: 'accounts', value: true });

        let path = '/fb-accounts?&currency=USD&perPage=100';
        const { meta } = state.accounts;

        if (nextPage) {
          path += `&page=${meta.currentPage + 1}`;
        }

        const { status, data } = await this._vm.api(path);
  
        if (status === 200 && data && Array.isArray(data.data)) {
          if (nextPage) {
            commit('ADD_ACCOUNTS', data.data);
          } else {
            commit('SET_ACCOUNTS', data.data);
          }
          commit('SET_ACCOUNTS_META', data.meta);
        }
      } catch (error) {
        // 
      } finally {
        commit('SET_LOADING', { key: 'accounts', value: false });
      }
    },

    async loadComments({commit}) {
      const response = await this._vm.api.get('/tracker/conversions');
      if (typeof response.data.data !== 'undefined') {
        commit('SET_COMMENTS', response.data.data);
      }
    },

    async setFiltersDates(context, dates) {
      context.commit('SET_FILTERS_DATES', dates);
    },

    async setFiltersAccount(context, data) {
      context.commit('SET_FILTERS_ACCOUNT', data);
    },

    async setFiltersTags(context, data) {
      context.commit('SET_FILTERS_TAGS', data);
    },

    async clearFilters(context) {
      await context.commit('CLEAR_FILTERS');
    },

    async setSettingsProperty({ commit }, data) {
      await commit('SET_SETTINGS_PROPERTY', data);
    },

    async replaceFbComment({ commit }, data) {
      await commit('REPLACE_FB_COMMENT', data);
    },
  },
};
